
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fill } = wp.components;

/**
 * Internal dependencies
 */
import { ToolbarRepeater } from 'Toolbar';



export default class TheToolbarContainer extends React.Component {

  render() {
    //
    return (
      <Fill name="Popover">
        <div
          className="editor-block-toolbar the-toolbar"
          data-index={ this.props.index }
          data-target={ this.props.name }
        >
          { this.props.children }

          { this.props.withRepeaterToolbar &&
            <ToolbarRepeater {...this.props} toBeHost />
          }

        </div>
      </Fill>
    );
  }

}




/*
|‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒
| Handle show/hide of TheToolbar
|‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒
*/
jQuery(document).ready(function($) {

  // Disable links to leave page
  //
  $(document).on('click', '.section a', function(e) {
    e.preventDefault();
    //return false;
  });

  // Hide toolbar function
  //
  var hideTheToolbar = function() {
    $('[data-show-toolbar]').removeClass('the-toolbar-target');
    $('.the-toolbar').removeClass('is-visible').css({ top: '-100px' });
  };


  // Show toolbar
  //
  $(document).on('click', '[data-show-toolbar]', function(e) {
    var el = $(this);

    // We need this timeout to run the code after Reactjs OnClick.
    setTimeout( function(){


      hideTheToolbar();

      var //el = $(this),
          target = el.attr('data-show-toolbar'),
          toolbar = $('.the-toolbar[data-target="'+ target +'"]'),
          position = el.offset();

      position.top -= window.pageYOffset;

      el.addClass('the-toolbar-target');
      toolbar.css(position).addClass('is-visible');

      $('.editor-rich-text__inline-toolbar').each( function(e) {
        if ( $(this).closest('[data-show-toolbar].the-toolbar-target').length === 0 ) {
          $(this).css('visibility', 'hidden');
        }
      });

    }, 1);


  });


  // Hide toolbar on next click
  //
  $(document).on('click', function(e) {
    var el = $(e.target);

    if ( el.closest('.editor-rich-text').length == 1 ) {

      $('.editor-rich-text__inline-toolbar').css('visibility', 'visible');
      return;
    }

    if ( el.closest('.the-toolbar, .components-popover, .media-modal').length == 0 ) {
      var target = $('.the-toolbar.is-visible').attr('data-target');
      if ( el.closest('[data-show-toolbar="'+ target +'"]').length == 0 ) {
        hideTheToolbar();
      }
    }
  });

});


// Change toolbar position after scroll
//
document.addEventListener('scroll', function (event) {
  if (event.target.classList !== undefined && event.target.classList[0] === 'edit-post-layout__content') {
    if ( jQuery('.the-toolbar-target').length ) {
      var pos = jQuery('.the-toolbar-target').offset();
      jQuery('.the-toolbar.is-visible').css('top', pos.top);
    }
  }
}, true);
