/**
 * External dependencies
 */
import classnames from 'classnames';


export default function Video(props) {
  const X = props.options;

  if ( X.source !== 'hosted' ) {
    return false;
  }

  let classes = classnames( "bg-video" );
  let data = {
    src: X.hosted,
    poster: X.poster,
    muted: X.mute,
    autoplay: "true",
    loop: "true",
  }

  return (
    <video class={ classes } { ...data } />
  );
}
