/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;
const { Toolbar, DropdownMenu } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar } from 'Components';
import * as Util from 'Utils';



export default class ToolbarNavbar extends TheToolbar {

  render() {
    const { attributes, name } = this.props;
    const X = this.X();

    return (
      <Fragment>
        <Toolbar
          controls={ [
            {
              icon: Util.Icons.SVG('section-header'),
              title: __( 'Hide divider' ),
              isActive: ! X.divider,
              onClick: () => this.set( 'divider', ! X.divider ),
            },
          ] }
        />

        <Toolbar>

          <DropdownMenu
            icon="editor-table"
            label={ __( 'Navbar type' ) }
            position="top center"
            controls={[
              {
                title: __( 'Static' ),
                isActive: X.type === 'static',
                onClick: () => this.set('type', 'static'),
              },
              {
                title: __( 'Fixed' ),
                isActive: X.type === 'fixed',
                onClick: () => this.set('type', 'fixed'),
              },
              {
                title: __( 'Sticky' ),
                isActive: X.type === 'sticky',
                onClick: () => this.set('type', 'sticky'),
              },
              {
                title: __( 'Smart' ),
                isActive: X.type === 'smart',
                onClick: () => this.set('type', 'smart'),
              },
            ]}
          />

          <DropdownMenu
            icon="editor-table"
            label={ __( 'Expand' ) }
            position="top center"
            controls={[
              {
                title: __( 'XL' ),
                isActive: X.expand === 'xl',
                onClick: () => this.set('expand', 'xl'),
              },
              {
                title: __( 'LG' ),
                isActive: X.expand === 'lg',
                onClick: () => this.set('expand', 'lg'),
              },
              {
                title: __( 'MD' ),
                isActive: X.expand === 'md',
                onClick: () => this.set('expand', 'md'),
              },
              {
                title: __( 'SM' ),
                isActive: X.expand === 'sm',
                onClick: () => this.set('expand', 'sm'),
              },
            ]}
          />

        </Toolbar>
      </Fragment>
    );
  }

}
