/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Image extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames({
      [ X.radius ] : !! X.radius,
    }, X.class);

    let data = {
      src: X.src,
      alt: X.alt,
      //'data-aos': X.data.aos,
      'data-show-toolbar': this.props.name,
    };

    return (
      <Fragment>
        <Toolbar {...this.props} />

        <img class={ classes } {...data} />
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}


/*
|‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒
| Link
|‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒‒
*/
function Link(props) {

  if ( ! props.url ) {
    return props.children;
  }

  let data = {};

  if ( props.newTab ) {
    data['target'] = '_blank';
  }

  if ( ! props.editable ) {
    data['href'] = props.url;
  }

  return (
    <a { ...data }>{ props.children }</a>
  );
}
