/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">
          <div class="col-lg-8 mx-auto">

            <ol class="timeline">
              <Repeater name="steps" {...props}

                content={ (item, set) => (
                  <li class="timeline-item" {...item.data}>
                    <RichText
                      tagName="h5"
                      inlineToolbar
                      onChange={ (v) => set('title', v) }
                      value={ item.title }
                    />
                    <p class="small-2">
                      <RichText
                        tagName="time"
                        inlineToolbar
                        onChange={ (v) => set('date', v) }
                        value={ item.date }
                      />
                    </p>
                    <p><Image {...item.data} name="img" /></p>
                    <RichText
                      tagName="p"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                  </li>
                )}

              />
            </ol>

          </div>
        </div>
      </Section>
    );
  }

}
