
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import * as Util from 'Utils';



export default class ToolbarSectionHeader extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar
          controls={ [
            {
              icon: Util.Icons.SVG('hr'),
              title: __( 'Hide horizontal line' ),
              isActive: ! X.hr,
              onClick: () => this.set( 'hr', ! X.hr ),
            },
          ] }
        />

      </TheToolbarContainer>
    );
  }


}
