/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">
          <div class="col-md-6">
            <h1 class="fw-200 mb-6"><span class="dynamic-placeholder">Post Title</span></h1>
            <p class="lead-2"><span class="dynamic-placeholder">Sub Title</span></p>

            <hr class="w-50px ml-0"/>

            <RichText
              tagName="p"
              className="small link-muted"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtLink: v }) }
              value={ attributes.txtLink }
            />
          </div>
        </div>
      </Section>
    );
  }

}
