/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Icon, Button, Image } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y">

          <div class="col-md-8 mx-auto mb-7">
            <Image {...props} name="img" />
          </div>

          <div class="w-100"></div>

          <Repeater name="features" {...props}

            content={ (item, set) => (
              <div class="col-md-6 col-xl-4" {...item.data}>
                <div class="media">
                  <div class="line-height-1 w-70px">
                    <Icon {...item.data} name="icon" />
                  </div>

                  <div class="media-body">
                    <RichText
                      tagName="h5"
                      inlineToolbar
                      onChange={ (v) => set('title', v) }
                      value={ item.title }
                    />
                    <RichText
                      tagName="p"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                  </div>
                </div>
              </div>
            )}

          />

          <div class="col-12 text-center">
            <br/><br/>
            <Button {...props} name="btn" />
          </div>

        </div>
      </Section>
    );
  }

}
