/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;
const { Button, Modal, TextControl } = wp.components;

/**
 * Internal dependencies
 */
import { Divider, Row } from 'Editor';



export default class PickerGradient extends React.Component {

  constructor() {
    super( ...arguments );
    this.state = {
      isOpen: false,
      gradient: this.props.value,
      keyword: '',
    }

    this.openModal = this.openModal.bind( this );
    this.closeModal = this.closeModal.bind( this );
    this.select = this.select.bind( this );
  }

  openModal() {
    if ( ! this.state.isOpen ) {
      this.setState( { isOpen: true } );
    }
  }

  closeModal() {
    if ( this.state.isOpen ) {
      this.setState( { isOpen: false } );
    }
  }

  select(value) {
    this.setState( { gradient: value } );
    this.props.onChange( value );
    //this.closeModal();
  }


  render() {
    return (
      <Fragment>
        <div className="the-gradient-picker-button">
          <label onClick={ this.openModal }>{ __( 'Gradient style' ) }</label>
          { !! this.state.gradient &&
            <Button isLink onClick={ () => this.setState( { gradient: '' } ) }>{ __( 'Clear' ) }</Button>
          }
          { ! this.state.gradient &&
            <Button isLink onClick={ this.openModal }>{ __( 'Select' ) }</Button>
          }
          <Gradient value={ this.state.gradient } isSmall onClick={ this.openModal } />
        </div>

        { this.state.isOpen ?
          <Modal className="the-gradient-picker-modal" title={ __( 'Select a gradient' ) } onRequestClose={ this.closeModal }>
            <div>
              <TextControl
                value={ this.state.gradient }
                onChange={ (v) => this.select( v ) }
                placeholder={ __( 'Gradient CSS' ) }
                />
            </div>

            <div className="the-gradient-picker">

              {[...gradients()].map( ( gradient ) =>
                <Gradient
                  value={ gradient }
                  state={ this.state }
                  onClick={ this.select }
                />
              )}

            </div>
          </Modal>
          : null }
      </Fragment>
    );
  }

}

function Gradient(props) {
  let gradient = props.value,
      state = props.state;

  if ( gradient === undefined ) {
    return false;
  }

  /*
  if ( state.keyword !== "" ) {
    if ( gradient.indexOf( state.keyword ) == -1 ) {
      return false;
    }
  }
  */

  let classes = classnames( 'the-gradient-picker-item', {
    'selected': !! state && gradient == state.gradient,
    'is-small': !! props.isSmall,
  });

  return (
    <div
      className={ classes }
      onClick={ () => props.onClick( gradient ) }
      style={{ 'backgroundImage': gradient }}
    />
  );
}

function gradients() {
  return [
    "linear-gradient(45deg, #ff9a9e 0%, #fad0c4 99%, #fad0c4 100%)",
    "linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%)",
    "linear-gradient(to top, #fad0c4 0%, #ffd1ff 100%)",
    "linear-gradient(to right, #ff8177 0%, #ff867a 0%, #ff8c7f 21%, #f99185 52%, #cf556c 78%, #b12a5b 100%)",
    "linear-gradient(to top, #ff9a9e 0%, #fecfef 99%, #fecfef 100%)",

    "linear-gradient(120deg, #f6d365 0%, #fda085 100%)",
    "linear-gradient(120deg, #a1c4fd 0%, #c2e9fb 100%)",
    "linear-gradient(120deg, #84fab0 0%, #8fd3f4 100%)",
    "linear-gradient(120deg, #f093fb 0%, #f5576c 100%)",
    "linear-gradient(to right, #4facfe 0%, #00f2fe 100%)",

    "linear-gradient(to top, #5ee7df 0%, #b490ca 100%)",
    "radial-gradient(circle 248px at center, #16d9e3 0%, #30c7ec 47%, #46aef7 100%)",
    "linear-gradient(120deg, #89f7fe 0%, #66a6ff 100%)",
    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
    "linear-gradient(to right, #6a11cb 0%, #2575fc 100%)",

    "linear-gradient(to right, #434343 0%, black 100%)",
    "linear-gradient(to top, #09203f 0%, #537895 100%)",
    "linear-gradient(to right, #868f96 0%, #596164 100%)",
    "linear-gradient(to right, #ff758c 0%, #ff7eb3 100%)",
    "linear-gradient(-225deg, #5D9FFF 0%, #B8DCFF 48%, #6BBBFF 100%)",
  ];
}

