
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarRepeater extends TheToolbar {

  render() {

    if ( ! this.props.isSelected || this.props.readonly ) {
      return null;
    }


    if ( this.props.toBeHost ) {
      return this.repeaterToolbar();
    }

    const { attributes, setAttributes, name, index } = this.props;

    let switchable = false;
    if ( index > -1 && attributes[ name ] !== undefined && attributes[ name ][ index ] !== undefined && attributes[ name ][ index ]['switch'] !== undefined ) {
      switchable = true;
    }


    const onDuplicate = ( index ) => {
      let nextItems = attributes[ name ].slice(0);
      nextItems.splice( index+1, 0, JSON.parse( JSON.stringify( nextItems[index] ) ) );
      setAttributes( { [ name ]: nextItems } );
    }

    const onRemove = ( index ) => {
      let nextItems = attributes[ name ].slice(0);
      nextItems.splice( index, 1 );
      setAttributes( { [ name ]: nextItems } );
    }

    const onSwitch = ( index ) => {
      let nextItems = attributes[ name ].slice(0);
      nextItems[ index ]['switch'] = ! nextItems[ index ]['switch'];
      setAttributes( { [ name ]: nextItems } );
    }

    return (
      <TheToolbarContainer {...this.props}>
        { this.props.children }

        <Toolbar>

          { switchable &&
            <IconButton
              className="components-toolbar__control"
              label={ __( 'Switch Sides' ) }
              icon="leftright"
              onClick={ () => onSwitch(index) }
            />
          }

          <IconButton
            className="components-toolbar__control"
            label={ __( 'Duplicate' ) }
            icon="admin-page"
            onClick={ () => onDuplicate(index) }
          />

          <IconButton
            className="components-toolbar__control"
            label={ __( 'Remove' ) }
            icon="no-alt"
            onClick={ () => onRemove(index) }
          />
        </Toolbar>
      </TheToolbarContainer>
    );
  }



  repeaterToolbar() {
    const { attributes, setAttributes, name, index, parent } = this.props;

    const onDuplicate = ( index ) => {
      let nextItems = attributes[ parent ].slice(0);
      nextItems.splice( index+1, 0, JSON.parse( JSON.stringify( nextItems[index] ) ) );
      setAttributes( { [ parent ]: nextItems } );
    }


    const onRemove = ( index ) => {
      let nextItems = attributes[ parent ].slice(0);
      nextItems.splice( index, 1 );
      setAttributes( { [ parent ]: nextItems } );
    }

    return(
      <Toolbar>
        <IconButton
          className="components-toolbar__control"
          label={ __( 'Duplicate' ) }
          icon="admin-page"
          onClick={ () => onDuplicate(index) }
        />

        <IconButton
          className="components-toolbar__control"
          label={ __( 'Remove' ) }
          icon="no-alt"
          onClick={ () => onRemove(index) }
        />
      </Toolbar>
    );
  }


}
