/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Icon, Button, Image } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">
          <div class="col-md-8 mb-6 mb-md-0">
            <Image {...props} name="img" />
          </div>


          <div class="col-md-4">
            <RichText
              tagName="h5"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />

            <ul class="project-detail mt-7">
              <Repeater name="details" {...props}

                content={ (item, set) => (
                  <li {...item.data}>
                    <RichText
                      tagName="strong"
                      inlineToolbar
                      onChange={ (v) => set('title', v) }
                      value={ item.title }
                    />
                    <RichText
                      tagName="span"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                  </li>
                )}

              />
            </ul>
          </div>

        </div>
      </Section>
    );
  }

}
