/**
 * External dependencies
 */
import classnames from 'classnames';


export default function Particles(props) {
  const X = props.options;

  if ( ! X.visible ) {
    return false;
  }

  let classes = classnames( "constellation" );

  let data = {
    'data-color': X.color,
    'data-length': X.length,
    'data-radius': X.radius,
  }

  return (
    <canvas class={ classes } { ...data } />
  );
}
