/**
 * External dependencies
 */
import classnames from 'classnames';


export default function Overlay(props) {
  const X = props.options;

  if ( X.opacity == 0 ) {
    return false;
  }

  let classes = classnames( "overlay", "opacity-"+ X.opacity );
  let styles = {};

  if ( X.type === 'color' ) {
    styles['backgroundColor'] = X.color;
  }
  else if ( X.type === 'gradient' ) {
    styles['backgroundImage'] = X.gradient;
  }

  return (
    <div class={ classes } style={ styles } />
  );
}
