
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, ColorPalette } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { PickerIcon } from './picker-icon';
import { Popup } from './popup';
import * as Util from 'Utils';



export default class ToolbarIcon extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>
        <Toolbar>

          {/* Icon */}
          <Popup label={ __( 'Icon' ) } icon="image-filter">
            <PickerIcon
              value={ X.icon }
              onChange={ (v) => this.set( "content", v ) }
            />
          </Popup>


          {/* Color */}
          <Popup label={ __( 'Color' ) } icon="art">
            <ColorPalette
              className="the-popover-color-palette"
              colors={ Util.Collection.Colors }
              value={ X.iconColor }
              onChange={ (v) => this.set( 'color', v ) }
            />
          </Popup>

        </Toolbar>
      </TheToolbarContainer>
    );
  }


}
