
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarSectionHeader extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    let { attributes, name } = this.props;

    if ( this.props.dataPrefix !== undefined ) {
      name = this.props.dataPrefix;
    }

    const attr = Util.Attr.Filter( attributes, name +'_' );


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar
          controls={ [
            {
              icon: Util.Icons.SVG('hr'),
              title: __( 'Remove horizontal line' ),
              isActive: ! attr.headerHr,
              onClick: () => this.set( 'headerHr', ! attr.headerHr ),
            },
          ] }
        />

      </TheToolbarContainer>
    );
  }


}
