/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Footer, Image, Nav, SocialIcons } from 'Components';
import { Repeater } from 'Editor';
import { ToolbarRepeaterLink } from 'Toolbar';


export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Footer {...props} name="footer">
        <div class="row gap-y align-items-center">

          <div class="col-md-3 text-center text-md-left">
            <Image {...props} name="imgLogo" />
          </div>

          <div class="col-md-9">
            <div class="nav nav-bold nav-uppercase justify-content-center justify-content-md-end">
              <Repeater {...props} name="links"
                content={ (item, set) => (
                  <a class="nav-link" href="#" {...item.data}>{ item.text }</a>
                )}

                toolbar={ (item, set) => (
                  <ToolbarRepeaterLink attributes={ item } setAttributes={ set } />
                )}
              />
            </div>
          </div>

          <div class="col-12">
            <hr class="my-0"/>
          </div>

          <div class="col-md-3 text-center text-md-left">
            <RichText
              tagName="small"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
          </div>

          <div class="col-md-9 text-center text-md-right">
            <SocialIcons {...props} name="social" />
          </div>

        </div>
      </Footer>
    );
  }

}
