
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl, SelectControl, ToggleControl, RangeControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarSlickSlider extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer {...this.props}>

        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control rotate-90"
                label={ __( 'More options' ) }
                icon="ellipsis"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <ToggleControl
                  label={ __( 'Autoplay' ) }
                  checked={ X.autoplay }
                  onChange={ (v) => this.set( 'autoplay', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Dot navigation' ) }
                  checked={ X.dots }
                  onChange={ (v) => this.set( 'dots', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Arrow navigation' ) }
                  checked={ X.arrows }
                  onChange={ (v) => this.set( 'arrows', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Pause on hover' ) }
                  checked={ X.pauseOnHover }
                  onChange={ (v) => this.set( 'pauseOnHover', v ) }
                />

                <hr />

                <RangeControl
                  label={ __( 'Slides to show' ) }
                  value={ X.slidesToShow }
                  onChange={ (v) => this.set( 'slidesToShow', v ) }
                  min={ 1 }
                  max={ 9 }
                  step="1"
                />

                <hr />

                <TextControl
                  label={ __( 'Extra class' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
