/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
          <div class="row">
            <div class="col-md-8 mx-auto">

              <div class="accordion accordion-connected accordion-arrow-right" id="accordion-faq">

                <Repeater name="faqs" {...props}

                  content={ (item, set) => (
                    <div class="card mt-0" {...item.data}>
                      <h5 class="card-title">
                        <a class="collapsed" data-toggle="collapse" href={ '#collapse-faq-'+ item.data.key }>
                          <RichText
                            inlineToolbar
                            onChange={ (v) => set('question', v) }
                            value={ item.question }
                          />
                        </a>
                      </h5>

                      <div id={ 'collapse-faq-'+ item.data.key } class="collapse" data-parent="#accordion-faq">
                        <RichText
                          tagName="div"
                          className="card-body"
                          inlineToolbar
                          onChange={ (v) => set('answer', v) }
                          value={ item.answer }
                        />
                      </div>
                    </div>
                  )}

                />

              </div>

            </div>
          </div>
      </Section>
    );
  }

}
