/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Icon extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames({
      [ X.content ] : !! X.content,
    }, X.class);

    let style = {
      'color': X.color,
    };

    let data = {
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <i class={ classes } style={ style } {...data}></i>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
