
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarMailer extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar>

          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Reciever Email' ) }
                  placeholder={ __( 'Default is admin email address' ) }
                  value={ X.receiver }
                  onChange={ (v) => this.set( 'receiver', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Subject' ) }
                  value={ X.subject }
                  onChange={ (v) => this.set( 'subject', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Success message' ) }
                  value={ X.message }
                  onChange={ (v) => this.set( 'message', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Extra class' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
