
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, ColorPalette } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { PickerIcon } from './picker-icon';
import { Popup } from './popup';
import * as Util from 'Utils';



export default class ToolbarRepeaterIcon extends TheToolbar {

  render() {

    const { attributes, setAttributes } = this.props;

    return (
      <Toolbar>

        {/* Icon */}
        <Popup label={ __( 'Icon' ) } icon="image-filter">
          <PickerIcon
            value={ attributes.icon }
            onChange={ (v) => setAttributes( "icon", v ) }
          />
        </Popup>


        {/* Color */}
        <Popup label={ __( 'Color' ) } icon="art">
          <ColorPalette
            className="the-popover-color-palette"
            colors={ Util.Collection.Colors }
            value={ attributes.iconColor }
            onChange={ (v) => setAttributes( 'iconColor', v ) }
          />
        </Popup>

      </Toolbar>
    );
  }


}
