
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, RangeControl, TextControl, Dropdown, SelectControl, ToggleControl } = wp.components;
const { URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarMap extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();

    return (
      <TheToolbarContainer {...this.props} >


        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top right"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Coordinates' ) }
                icon="location"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Latitude' ) }
                  value={ X.lat }
                  onChange={ (v) => this.set( 'lat', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Longitude' ) }
                  value={ X.lng }
                  onChange={ (v) => this.set( 'lng', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Show Marker' ) }
                  checked={ X.marker }
                  onChange={ (v) => this.set( 'marker', v ) }
                />

              </div>
            ) }
          />


          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top right"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control rotate-90"
                label={ __( 'More options' ) }
                icon="ellipsis"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">
                <RangeControl
                  label={ __( 'Height' ) }
                  value={ X.height }
                  onChange={ (v) => this.set( 'height', v ) }
                  min={ 100 }
                  max={ 600 }
                  step="10"
                />

                <hr />

                <RangeControl
                  label={ __( 'Zoom' ) }
                  value={ X.zoom }
                  onChange={ (v) => this.set( 'zoom', v ) }
                  min={ 0 }
                  max={ 16 }
                  step="1"
                />

                <hr />

                <SelectControl
                  label={ __( 'Theme' ) }
                  value={ X.theme }
                  onChange={ (v) => { this.set( 'theme', v ) } }
                  options={ [
                    { value: '', label: 'Default' },
                    { value: 'light', label: 'Light' },
                    { value: 'dark', label: 'Dark' },
                  ] }
                />

              </div>
            ) }
          />
        </Toolbar>


      </TheToolbarContainer>
    );
  }


}
