/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, GoogleMap, SocialIcons } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  componentDidMount(){
    if ( window["google.maps"] === undefined ) {
      jQuery.getScript("https://maps.googleapis.com/maps/api/js?key="+ page.defaults.googleApiKey +"&callback=page.initMap");
    }
    else {
      page.initMap( $(this.refs.target) );
    }
  }

  componentDidUpdate(prevProps) {
    if ( window["google.maps"] === undefined ) {
      page.initMap( $(this.refs.target) );
    }
  }

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y align-items-center">

          <div class="col-md-5">
            <RichText
              tagName="p"
              className="text-uppercase small opacity-70 fw-600 ls-1"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtSmall: v }) }
              value={ attributes.txtSmall }
            />
            <RichText
              tagName="h5"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <br/>
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
            <br/>
            <RichText
              tagName="h6"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitleFollow: v }) }
              value={ attributes.txtTitleFollow }
            />

            <SocialIcons name="social" {...props} />
          </div>


          <div class="col-md-7">
            <GoogleMap {...props} name="map" ref="target" />
          </div>

        </div>
      </Section>
    );
  }

}
