/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarSection extends TheToolbar {

  render() {
    const X = this.X();

    if ( X.isHeaderTag ) {
      return null;
    }

    return (
      <Toolbar
        controls={ [
          {
            icon: Util.Icons.SVG('section-header'),
            title: __( 'Hide section header' ),
            isActive: X.header.visible,
            onClick: () => this.set( 'header.visible', ! X.header.visible ),
          },
        ] }
      />
    );
  }

}
