/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">

        <Repeater name="steps" {...props}

          content={ (item, set) => (
            <div class="row gap-y align-items-center py-7" {...item.data}>
              { ! item.switch &&
                <div class="col-lg-6 mr-auto text-center">
                  <Image {...item.data} name="img" />
                </div>
              }

              <div class="col-lg-5 text-center text-lg-left">
                <RichText
                  tagName="p"
                  className="lead-9 fw-900 lh-1 opacity-10"
                  inlineToolbar
                  onChange={ (v) => set('number', v) }
                  value={ item.number }
                />
                <RichText
                  tagName="h3"
                  inlineToolbar
                  onChange={ (v) => set('title', v) }
                  value={ item.title }
                />
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => set('text', v) }
                  value={ item.text }
                />
              </div>

              { item.switch &&
                <div class="col-lg-6 ml-auto text-center order-first order-lg-last">
                  <Image {...item.data} name="img" />
                </div>
              }
            </div>
          )}

        />

      </Section>
    );
  }

}
