/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y align-items-center">

          { attributes.section.switchSides &&
            <div class="col-md-6 mr-auto">
              <Image {...props} name="imgCover" />
            </div>
          }

          <div class="col-md-5 text-center text-md-left">
            <RichText
              tagName="h2"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />

            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />

            <div class="media align-items-center mt-7 text-left">
              <div class="mr-4">
                <Button {...props} name="btnPlay" />
              </div>
              <div class="media-body">
                <a class="small text-default text-uppercase mb-0" href={ attributes.btnPlay.url }>
                  <RichText
                    tagName="strong"
                    inlineToolbar
                    onChange={ (v) => setAttributes({ txtPlay: v }) }
                    value={ attributes.txtPlay }
                  />
                </a>
              </div>
            </div>
          </div>

          { ! attributes.section.switchSides &&
            <div class="col-md-6 ml-auto">
              <Image {...props} name="imgCover" />
            </div>
          }


        </div>
      </Section>
    );
  }

}
