/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { TheBgImageUploader } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    const style = {
      backgroundImage: "url("+ attributes.imgBgPlay +")",
      minHeight: "300px",
      position: "relative",
    }

    return (
      <Section {...props} name="section">
        <div class="row no-gutters">

          <div class="col-md-6 bg-img video-btn-wrapper order-md-2" style={ style } data-overlay="3">
            <TheBgImageUploader name="imgBgPlay" value={ attributes.imgBgPlay } setAttributes={ setAttributes } />
            <Button {...props} name="btnPlay" />
          </div>

          <div class="col-10 col-md-4 mx-auto py-7 py-md-9">
            <RichText
              tagName="h5"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              className="mb-0"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
          </div>

        </div>
      </Section>
    );
  }

}
