
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, Dropdown, SelectControl } = wp.components;
const { MediaUpload, URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { Popup } from './popup';
import * as Util from 'Utils';



export default class ToolbarImage extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const { attributes, name } = this.props;
    const X = this.X();


    const onSelectMedia = ( media ) => {
      if ( ! media || ! media.url ) {
        return;
      }

      this.set('src', media.url);
    }


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar>


          <MediaUpload
            onSelect={ onSelectMedia }
            allowedTypes="image"
            value={ X.src }
            render={ ( { open } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Edit image' ) }
                icon="format-image"
                onClick={ open }
              />
            ) }
          />


          {/* Alt Text */}
          <Popup label={ __( 'Image alt text' ) } icon="edit">
            <input
              className="components-text-control__input rounded-0"
              type="text"
              value={ X.alt }
              onChange={ (v) => this.set('alt', v.target.value) }
            />
          </Popup>


          {/* Link */}
          <Popup label={ __( 'Link' ) } icon="admin-links">
            <form
              className="block-library-button__inline-link the-url-input-form"
              onSubmit={ ( event ) => event.preventDefault() }>
              <URLInput
                value={ X.url }
                onChange={ (v) => this.set('url', v) }
              />
              <IconButton
                className={ 'external-'+ X.targetBlank }
                label={ __( 'Open in new tab' ) }
                icon="external"
                onClick={ () => this.set('targetBlank', ! X.targetBlank) }
              />
            </form>
          </Popup>

        </Toolbar>


      {/* Animation */}
        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control rotate-90"
                label={ __( 'More options' ) }
                icon="ellipsis"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <SelectControl
                  label={ __( 'Animation on scroll' ) }
                  value={ X.data.aos }
                  onChange={ (v) => { this.set( 'data.aos', v ) } }
                  options={ [
                    { value: '', label: 'None' },
                    { value: 'fade', label: 'fade' },
                    { value: 'fade-up', label: 'fade-up' },
                  ] }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
