/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Input extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( 'form-control',{
      [`form-control-${X.size}`]: X.size !== 'md'
    }, X.class );

    let data = {
      type: X.type,
      id: X.id,
      name: X.name,
      value: X.value,
      placeholder: X.placeholder,
      autocomplete: "off",
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        { X.type !== "textarea" &&
          <input class={ classes } {...data} />
        }

        { X.type === "textarea" &&
          <textarea class={ classes } {...data} rows="4">{ X.value }</textarea>
        }

      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
