
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, Dropdown, SelectControl, TextControl, RangeControl } = wp.components;
const { MediaUpload, URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarImage extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const { attributes, name } = this.props;
    const X = this.X();


    const onSelectMedia = ( media ) => {
      if ( ! media || ! media.url ) {
        return;
      }

      this.set('src', media.url);
    }

    return (
      <TheToolbarContainer {...this.props}>

        <Toolbar>


          <MediaUpload
            onSelect={ onSelectMedia }
            allowedTypes="image"
            value={ X.src }
            render={ ( { open } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Edit image' ) }
                icon="format-image"
                onClick={ open }
              />
            ) }
          />


          {/* Alt Text */}
          <Popup label={ __( 'Image alt text' ) } icon="edit" position="top center">
            <input
              className="components-text-control__input rounded-0"
              type="text"
              value={ X.alt }
              onChange={ (v) => this.set('alt', v.target.value) }
            />
          </Popup>


          {/* Link */}
          <Popup label={ __( 'Link' ) } icon="admin-links" position="top center">
            <form
              className="block-library-button__inline-link the-url-input-form"
              onSubmit={ ( event ) => event.preventDefault() }>
              <URLInput
                value={ X.url }
                onChange={ (v) => this.set('url', v) }
              />
              <IconButton
                className={ 'external-'+ X.targetBlank }
                label={ __( 'Open in new tab' ) }
                icon="external"
                onClick={ () => this.set('targetBlank', ! X.targetBlank) }
              />
            </form>
          </Popup>


          {/* Animation */}
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Animation' ) }
                icon="images-alt2"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <SelectControl
                  label={ __( 'Effect' ) }
                  value={ X.data.aos }
                  onChange={ (v) => { this.set( 'data.aos', v ) } }
                  options={ [
                    { value: '', label: 'None' },
                    { value: 'fade', label: 'Fade' },
                    { value: 'fade-up', label: 'Fade Up' },
                    { value: 'fade-down', label: 'Fade Down' },
                    { value: 'fade-left', label: 'Fade Left' },
                    { value: 'fade-right', label: 'Fade Right' },
                    { value: 'slide-up', label: 'Slide Up' },
                    { value: 'slide-down', label: 'Slide Down' },
                    { value: 'slide-left', label: 'Slide Left' },
                    { value: 'slide-right', label: 'Slide Right' },
                    { value: 'zoom-in', label: 'Zoom In' },
                    { value: 'zoom-out', label: 'Zoom Out' },
                  ] }
                />

                <hr />

                <RangeControl
                  label={ __( 'Delay (ms)' ) }
                  value={ X.data['aos-delay'] || 50 }
                  onChange={ (v) => this.set( 'data.aos-delay', v ) }
                  min={ 0 }
                  max={ 1000 }
                  step="10"
                />

                <hr />

                <RangeControl
                  label={ __( 'Duration (ms)' ) }
                  value={ X.data['aos-duration'] || 1000 }
                  onChange={ (v) => this.set( 'data.aos-duration', v ) }
                  min={ 0 }
                  max={ 5000 }
                  step="50"
                />

              </div>
            ) }
          />

          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Extra CSS classes' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />

        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
