/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import * as block_1 from './block-1';
import * as block_2 from './block-2';
import * as block_3 from './block-3';
import * as block_4 from './block-4';
//import * as block_5 from './block-5';
import * as block_6 from './block-6';
//import * as block_7 from './block-7';
import * as block_8 from './block-8';
import * as block_9 from './block-9';
import * as block_10 from './block-10';
import * as block_11 from './block-11';
//import * as block_12 from './block-12';
import * as block_13 from './block-13';
import * as block_14 from './block-14';
import * as block_15 from './block-15';
import * as block_16 from './block-16';



/**
 * Register the blocks
 */
const slug = 'feature';
const slugCap = __( slug.charAt(0).toUpperCase() + slug.slice(1) );

[

  block_1,
  block_2,
  block_3,
  block_4,
  //block_5,
  block_6,
  //block_7,
  block_8,
  block_9,
  block_10,
  block_11,
  //block_12,
  block_13,
  block_14,
  block_15,
  block_16,

].forEach( ({ id, settings }) => {
  const name = 'thesaasx/'+ slug +'-'+ id;
  const moreSettings = {
    title: __( slugCap +' '+ id ),
    icon: {
      src: 'analytics',
    },
    category: 'thesaasx-'+ slug,
    supports: {
      customClassName : false,
      html: false,
    },
    keywords: [
      __( 'thesaasx' ),
    ],
  };

  Object.assign(settings, moreSettings);
  registerBlockType( name, settings );
});

