/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;
const { RichText } = wp.editor;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import { default as Toolbar } from './toolbar-header';



export default class SectionHeader extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <header class="section-header" data-show-toolbar={ this.props.name }>
          <RichText
            tagName="small"
            inlineToolbar
            value={ X.small }
            onChange={ (v) => this.set('small', v) }
          />
          <RichText
            tagName="h2"
            inlineToolbar
            value={ X.title }
            onChange={ (v) => this.set('title', v) }
          />
          { X.hr && <hr /> }
          { ! X.hr && <br /> }
          <RichText
            tagName="p"
            inlineToolbar
            value={ X.text }
            onChange={ (v) => this.set('text', v) }
          />
        </header>
      </Fragment>
    );
  }


}
