/**
 * External dependencies
 */

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;
const { PanelBody, ToggleControl, Button, ButtonGroup, ColorPalette, SelectControl, RangeControl, TextControl } = wp.components;
const { MediaUpload } = wp.editor;

/**
 * Internal dependencies
 */
import { Divider, Row, PickerGradient, Repeater, TheColorPalette } from 'Editor';
import * as Util from 'Utils';



export default class PanelNavbar extends React.Component {

  constructor(props) {
    super(props);

    this.set = this.set.bind( this );
  }

  set(key, value) {
    this.props.onChange( { [ this.props.name +'_'+ key ]: value } );
  }


  render() {
    const attr = Util.Attr.Filter( this.props.attributes, this.props.name +'_' );


    return (
      <Fragment>

        <PanelBody title={ __( 'Navbar Settings' ) } initialOpen={ true }>


          <SelectControl
            label={ __( 'Position type' ) }
            value={ attr.type }
            onChange={ (v) => this.set( 'type', v ) }
            options={[
              { value: 'static',   label: __( 'Static' ) },
              { value: 'fixed',    label: __( 'Fixed' ) },
              { value: 'sticky',   label: __( 'Sticky' ) },
              { value: 'smart',    label: __( 'Smart' ) },
            ]}
          />

          <SelectControl
            label={ __( 'Expand on' ) }
            value={ attr.expand }
            onChange={ (v) => this.set( 'expand', v ) }
            options={[
              { value: 'sm',   label: __( 'Phones' ) },
              { value: 'md',   label: __( 'Tablets' ) },
              { value: 'lg',   label: __( 'Desktops' ) },
              { value: 'xl',   label: __( 'Large desktops' ) },
            ]}
          />

          <SelectControl
            label={ __( 'Default color' ) }
            value={ attr.color }
            onChange={ (v) => this.set( 'color', v ) }
            options={[
              { value: 'dark',    label: __( 'Dark' ) },
              { value: 'light',   label: __( 'Light' ) },
            ]}
          />

          <SelectControl
            label={ __( 'Color on sticky' ) }
            value={ attr.stickColor }
            onChange={ (v) => this.set( 'stickColor', v ) }
            options={[
              { value: 'dark',    label: __( 'Dark' ) },
              { value: 'light',   label: __( 'Light' ) },
            ]}
          />


          <ToggleControl
            label={ __( 'Fluid container' ) }
            checked={ attr.containerFluid }
            onChange={ (v) => this.set( 'containerFluid', v ) }
          />

        </PanelBody>



        <PanelBody title={ __( 'Buttons' ) } initialOpen={ false }>


          <ToggleControl
            label={ __( 'Show button 1' ) }
            checked={ attr.showBtn1 }
            onChange={ (v) => this.set( 'showBtn1', v ) }
          />


          <ToggleControl
            label={ __( 'Show button 2' ) }
            checked={ attr.showBtn2 }
            onChange={ (v) => this.set( 'showBtn2', v ) }
          />


        </PanelBody>

      </Fragment>
    );
  }
}

