/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component, Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';


export default class Edit extends Component {

  constructor(props) {
    super(props);

    this.set = this.set.bind( this );
  }


  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };


    if (attributes.items_mo === undefined || attributes.items_mo.length === 0) {
      var items_mo = [
        {
          btn: attributes.btn1,
          plan: attributes.pricing1,
        },
        {
          btn: attributes.btn2,
          plan: attributes.pricing2,
        },
        {
          btn: attributes.btn3,
          plan: attributes.pricing3,
        }
      ];

      setAttributes({items_mo: items_mo});
    }


    if (attributes.items_yr === undefined || attributes.items_yr.length === 0) {
      var items_yr = [
        {
          btn: attributes.yr_btn1,
          plan: attributes.yr_pricing1,
        },
        {
          btn: attributes.yr_btn2,
          plan: attributes.yr_pricing2,
        },
        {
          btn: attributes.yr_btn3,
          plan: attributes.yr_pricing3,
        }
      ];

      setAttributes({items_yr: items_yr});
    }


    const getPlan = (item, set) => {

      return (
        <div class="col-md-6 col-lg-4 mx-auto" {...item.data}>
          <div class="pricing-1">
            <RichText
              tagName="p"
              className="plan-name"
              inlineToolbar
              onChange={ (v) => set('plan.name', v) }
              value={ item.plan.name }
            />
            <br/>
            <h2 class="price">
              <span class="d-inline-block mr-4">
                <RichText
                  tagName="span"
                  className="price-unit"
                  inlineToolbar
                  onChange={ (v) => set('plan.unit', v) }
                  value={ item.plan.unit }
                />
              </span>

              <span class="d-inline-block">
                <RichText
                  tagName="span"
                  inlineToolbar
                  onChange={ (v) => set('plan.price', v) }
                  value={ item.plan.price }
                />
              </span>
            </h2>
            <RichText
              tagName="p"
              className="small text-lighter"
              inlineToolbar
              onChange={ (v) => set('plan.period', v) }
              value={ item.plan.period }
            />

            <RichText
              tagName="div"
              className="small text-muted"
              inlineToolbar
              onChange={ (v) => set('plan.text', v) }
              value={ item.plan.text }
            />

            <br/>
            <p class="text-center py-3">
              <Button {...item.data} name="btn" />
            </p>
          </div>
        </div>
      );
    }


    return (
      <Section {...props} name="section">

        <div class="text-center">
          <label class="mb-4">
            <input
              type="checkbox"
              autocomplete="off"
              checked={ attributes.cycle.visible }
              onChange={ () => this.set( 'cycle.visible', ! attributes.cycle.visible )}
            />
            { __('Have monthly and yearly plans?') }
          </label>
        </div>

        <ul class={ "nav nav-tabs-outline mb-7"+ (attributes.cycle.visible ? '' : ' opacity-30') }>
          <li class="nav-item">
            <a class="nav-link w-1501 active" data-toggle="tab" href="#price-mo" style={{minWidth: 150}}>
              <RichText
                tagName="span"
                inlineToolbar
                onChange={ (v) => setAttributes({ txtMonthly: v }) }
                value={ attributes.txtMonthly }
              />
            </a>
          </li>
          <li class="nav-item">
            <a class="nav-link w-1501" data-toggle="tab" href="#price-yr" style={{minWidth: 150}}>
              <RichText
                tagName="span"
                inlineToolbar
                onChange={ (v) => setAttributes({ txtYearly: v }) }
                value={ attributes.txtYearly }
              />
            </a>
          </li>
        </ul>


        <div class="row gap-y text-center">

          { attributes.cycle.visible === true &&
            <div class="w-100">
              <div class="divider mt-8">{ attributes.txtMonthly + __(' Plans') }</div>
            </div>
          }


          <Repeater name="items_mo" {...props}
            content={ (item, set) => getPlan(item, set)}
          />


          { attributes.cycle.visible === true &&
            <Fragment>
              <div class="w-100">
                <div class="divider mt-8">{ attributes.txtYearly + __(' Plans') }</div>
              </div>

              <Repeater name="items_yr" {...props}
                content={ (item, set) => getPlan(item, set)}
              />
            </Fragment>
          }

        </div>
      </Section>
    );
  }


  set( key, value ) {

    let names   = key.split('.'),
        name    = names.shift(),
        current = this.props.attributes[ name ],
        next    = Object.assign( {}, current );

    next[ names[0] ] = value;
    this.props.setAttributes( { [ name ]: next } );
  }


}
