/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { ToolbarRepeater } from 'Toolbar';
import * as Util from 'Utils';



export default class Repeater extends TheComponent {

  constructor() {
    super( ...arguments );
    this.state = {
      selectedIndex: -1,
    }

    this.setSelected = this.setSelected.bind( this );
  }


  setSelected( index ) {
    this.setState({
      selectedIndex: index,
    });
  }


  render() {
    const { content, toolbar, attributes, name } = this.props;
    const { selectedIndex } = this.state;
    let items = this.X();

    /*
    if ( typeof items === "object" && !Array.isArray(items) && items !== null ) {
      items = Object.values( items );
    }
    */

    const repeaterItems = items.map( ( item, index ) => {
      let props = {};
      let data = {
        attributes: attributes,
        setAttributes: this.props.setAttributes,
        key: index,
        index: index,
        parent: name,
        isSelected: selectedIndex === index ? true : false,

        'data-index': index,
        'data-show-toolbar': name,

        onClick: () => this.setSelected( index ),
      };

      Object.assign( props, item, {data: data} );

      return content( props, (key, value) => this.setRepeaterItem(key, value, index, name) );
    });

    let toolbars = null;
    if ( selectedIndex > -1 && toolbar !== undefined ) {
      toolbars = toolbar( items[ selectedIndex ], (key, value) => this.setRepeaterItem(key, value, selectedIndex, name) );
    }


    return (
      <Fragment>
        <ToolbarRepeater {...Object.assign( this.props, {index: selectedIndex} )}>
          { toolbars }
        </ToolbarRepeater>

        { repeaterItems }
      </Fragment>

    );
  }

}

