/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, SocialIcons } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y">

          <Repeater name="reviews" {...props}

            content={ (item, set) => (
              <div class="col-md-4" {...item.data}>
                <div class="card shadow-3">
                  <div class="card-body px-6">
                    <div class="rating mb-3">
                      <label class="fa fa-star active"></label>
                      <label class="fa fa-star active"></label>
                      <label class="fa fa-star active"></label>
                      <label class="fa fa-star active"></label>
                      <label class="fa fa-star active"></label>
                    </div>

                    <RichText
                      tagName="p"
                      className="text-quoted mb-5"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                    <div class="media align-items-center pb-0">
                      <Image {...item.data} name="img" />
                      <div class="media-body lh-1">
                        <RichText
                          tagName="div"
                          className="fw-400 small-1 mb-1"
                          inlineToolbar
                          onChange={ (v) => set('name', v) }
                          value={ item.name }
                        />
                        <RichText
                          tagName="small"
                          className="text-lighter"
                          inlineToolbar
                          onChange={ (v) => set('user', v) }
                          value={ item.user }
                        />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            )}

          />

        </div>
      </Section>
    );
  }

}
