/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    let section = attributes.section;
    let colClass = 'col-lg-6 text-center text-lg-right';
    if ( section.switchSides ) {
      colClass = 'col-lg-6 text-center text-lg-left';
    }

    return (
      <Section {...props} name="section">
        <div class="row gap-y align-items-center">

          { section.switchSides &&
            <div class="col-lg-6">
              <Image {...props} name="img" />
            </div>
          }

          <div class={ colClass }>
            <RichText
              tagName="p"
              className="small-2 text-uppercase text-lightest fw-500 ls-1"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtSmall: v }) }
              value={ attributes.txtSmall }
            />
            <RichText
              tagName="h3"
              className="fw-500"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <br/>
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
          </div>

          { ! section.switchSides &&
            <div class="col-lg-6">
              <Image {...props} name="img" />
            </div>
          }

        </div>
      </Section>
    );
  }

}
