/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">

          <Repeater name="services" {...props}

            content={ (item, set) => (
              <div class="col-md-6 col-xl-5 mx-lg-auto" {...item.data}>
                <RichText
                  tagName="h6"
                  className="bt-2 border-secondary py-5"
                  inlineToolbar
                  onChange={ (v) => set('title', v) }
                  value={ item.title }
                />
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => set('text', v) }
                  value={ item.text }
                />
                <br/><br/>
              </div>
            )}

          />

        </div>
      </Section>
    );
  }

}
