/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Form extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let data = {
      action: X.action,
      mrthod: X.method,
      name: X.name,
      id: X.id,
      target: X.targetBlank ? '_blank' : '',
      autocomplete: X.autocomplete ? '' : 'off',
      'data-show-toolbar': this.props.name,
    };

    return (
      <Fragment>
        <Toolbar {...this.props} />

        <form class={ X.class } {...data}>
          { this.props.children }
        </form>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
