/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Countup } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y text-center">

          <Repeater name="numbers" {...props}

            content={ (item, set) => (
              <div class="col-6 col-lg-3" { ...item.data }>
                <p class="lead-8 mb-0">
                  <Countup name="countup" {...item.data} />
                </p>
                <RichText
                  tagName="p"
                  className="small text-uppercase ls-2"
                  inlineToolbar
                  onChange={ (v) => set('text', v) }
                  value={ item.text }
                />
              </div>
            )}

          />

        </div>
      </Section>
    );
  }

}
