
import * as Util from 'Utils';
const { ColorPalette } = wp.components;



export default function( props ) {
  return (
    <div className="the-color-palette components-base-control">
      <label class="components-base-control__label ">{ props.label }</label>
      <ColorPalette
        value={ props.value }
        colors={ Util.Collection.BackgroundColors }
        onChange={ (v) => props.onChange( v ) }
      />
    </div>
  );
}
