
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, Button, Dropdown, ButtonGroup, ColorPalette, RangeControl, TextControl, ToggleControl } = wp.components;
const { URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarButton extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();

    return (
      <TheToolbarContainer {...this.props} >

        <Toolbar>


          {/* Text */}
          <Popup label={ __( 'Text' ) } icon="edit">
            <input
              className="components-text-control__input rounded-0"
              type="text"
              placeholder={ __( 'Type button text' ) }
              value={ X.text }
              onChange={ (v) => this.set('text', v.target.value) }
            />
          </Popup>


          {/* Link */}
          <Popup label={ __( 'Link' ) } icon="admin-links">
            <form
              className="block-library-button__inline-link the-url-input-form"
              onSubmit={ ( event ) => event.preventDefault() }>
              <URLInput
                value={ X.url }
                onChange={ (v) => this.set('url', v) }
              />
              <IconButton
                className={ 'external-'+ X.targetBlank }
                label={ __( 'Open in new tab' ) }
                icon="external"
                onClick={ () => this.set('targetBlank', ! X.targetBlank) }
              />
            </form>
          </Popup>


          {/* Color */}
          <Popup label={ __( 'Color' ) } icon="art">
            <ColorPalette
              className="the-popover-color-palette"
              colors={ Util.Collection.Colors }
              disableCustomColors
              value={ Util.Color.Value( X.color ) }
              onChange={ (v) => this.set( 'color', Util.Color.Name(v) ) }
            />
          </Popup>


          {/* Size */}
          <Popup label={ __( 'Size' ) } icon="admin-settings">
            <ButtonGroup className="button-group-btn-sizes">
              { Util.Array.Sizes.map(x => (
                <Button key={x} isLarge isPrimary={ X.size === x }
                  onClick={ () => this.set( "size", x ) }
                  >
                  {x.toUpperCase()}
                </Button>
              )) }
            </ButtonGroup>
          </Popup>


          {/* Icon */}
          <Popup label={ __( 'Icon' ) } icon="image-filter">
            <PickerIcon
              value={ X.icon.content }
              onChange={ (v) => this.set( "icon.content", v ) }
            />
          </Popup>


          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">
                <ToggleControl
                  label={ __( 'Wide' ) }
                  checked={ X.block }
                  onChange={ (v) => this.set( 'block', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Round' ) }
                  checked={ X.round }
                  onChange={ (v) => this.set( 'round', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Outline' ) }
                  checked={ X.outline }
                  onChange={ (v) => this.set( 'outline', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Circle' ) }
                  checked={ X.circle }
                  onChange={ (v) => this.set( 'circle', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Glass' ) }
                  checked={ X.glass }
                  onChange={ (v) => this.set( 'glass', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Show in lightbox' ) }
                  checked={ X.data.provide === 'lightbox' ? true : false }
                  onChange={ (v) => v ? this.set( 'data.provide', 'lightbox' ) : this.set( 'data.provide', '' ) }
                />

                <hr />

                <RangeControl
                  label={ __( 'Minimum width' ) }
                  value={ X.minWidth }
                  onChange={ (v) => this.set( 'minWidth', v ) }
                  min={ 0 }
                  max={ 400 }
                  step="5"
                />

                <hr />

                <TextControl
                  label={ __( 'Extra CSS classes' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
