
( function( wp ) {
    var TypingFormatButton = function( props ) {
        return wp.element.createElement(
            wp.editor.RichTextToolbarButton, {
                icon: 'editor-paste-text',
                title: 'Typing Comma-Separated Text',
                onClick: function() {
                    props.onChange( wp.richText.toggleFormat(
                        props.value,
                        { type: 'thesaasx-format/typing' }
                    ) );
                },
                isActive: props.isActive,
            }
        );
    }
    wp.richText.registerFormatType(
        'thesaasx-format/typing', {
            title: 'Typing Comma-Separated Text',
            tagName: 'span',
            className: 'thesaasx-typing-text-format',
            edit: TypingFormatButton,
        }
    );
} )( window.wp );
