/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';



export default class AppLink extends TheComponent {
  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    const apple  = X.apple;
    const google = X.google;

    let dataApple = {
      href: apple.url,
      target: apple.targetBlank ? '_blank' : '',
    };

    let dataGoogle = {
      href: google.url,
      target: google.targetBlank ? '_blank' : '',
    };


    return (
      <Fragment>
        { !! apple.url &&
          <a { ...dataApple }>
            <img src={ apple.img } alt={ apple.alt } />
          </a>
        }

        { !! google.url &&
          <a { ...dataGoogle }>
            <img src={ google.img } alt={ google.alt } />
          </a>
        }
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
