/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import * as finance1 from './finance-1';
import * as marketing1 from './marketing-1';
import * as marketing2 from './marketing-2';
import * as saas1 from './saas-1';
import * as saas2 from './saas-2';
import * as saas3 from './saas-3';
//import * as saas4 from './saas-4';
import * as software1 from './software-1';
import * as software2 from './software-2';
import * as software3 from './software-3';



/**
 * Register the blocks
 */
const slug = 'demo';
const slugCap = __( slug.charAt(0).toUpperCase() + slug.slice(1) );

[

  finance1,
  marketing1,
  marketing2,
  saas1,
  saas2,
  saas3,
  software1,
  software2,
  software3,

].forEach( ({ id, settings }) => {
  const name = 'thesaasx/'+ slug +'-'+ id;
  const moreSettings = {
    title: settings.title,
    icon: {
      src: 'welcome-widgets-menus',
    },
    category: 'thesaasx-'+ slug,
    supports: {
      customClassName : false,
      html: false,
    },
    keywords: [
      __( 'thesaasx' ),
    ],
  };

  Object.assign(settings, moreSettings);
  registerBlockType( name, settings );
});

