/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;
const { RichText } = wp.editor;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Nav extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    if ( X.links === undefined ) {
      return null;
    }

    if ( ! Object.keys(X).length ) {
      return null;
    }

    let classes = classnames( 'nav', X.class );

    let links = Object.entries(X.links).map(([key, link]) => {

      let data = {
        href: "#"
      };

      return (<a className="nav-link" { ...data }>{link.text}</a>);
    })

    let data = {
      'data-show-toolbar': this.props.name
    };


    return (
      <Fragment>

        <div class={classes} {...data}>
          <Repeater {...this.props} name="nav.links"

            content={ (item, set) => (
              <div class="" {...item.data}>
                <RichText
                  tagName="span"
                  inlineToolbar
                  onChange={ (v) => set('text', v) }
                  value={ item.text }
                />
              </div>
            )}

          />
        </div>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
