/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class ReadMore extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames( X.class );
    let classesIcon = classnames( X.icon, X.iconClass );

    let data = {
      href: X.url,
      target: X.targetBlank ? '_blank' : '',
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <a className={ classes } { ...data }>{ X.text } <i class={ classesIcon }></i></a>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
