/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Button, ReadMore } from 'Components';
import { Repeater, TheBgImageUploader } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  constructor(props) {
    super(props);

    this.set = this.set.bind( this );
  }


  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };


    if (attributes.items_mo === undefined || attributes.items_mo.length === 0) {
      var items_mo = [
        {
          btn: attributes.btn1,
          plan: attributes.pricing1,
        },
        {
          btn: attributes.btn2,
          plan: attributes.pricing2,
        }
      ];

      setAttributes({items_mo: items_mo});
    }


    if (attributes.items_yr === undefined || attributes.items_yr.length === 0) {
      var items_yr = [
        {
          btn: attributes.yr_btn1,
          plan: attributes.yr_pricing1,
        },
        {
          btn: attributes.yr_btn2,
          plan: attributes.yr_pricing2,
        }
      ];

      setAttributes({items_yr: items_yr});
    }



    const getPlan = (id, cycle="") => {
      if ( cycle === 'yr' ) {
        cycle = 'yr_';
      }
      const name = cycle +'pricing'+ id;
      const plan = attributes[ name ];
      const imgName = cycle +'img'+ id;
      const img = attributes[ imgName ];
      const btnName = cycle +'btn'+ id;


      const style = {
        backgroundImage: "url("+ img +")",
        position: "relative",
      }

      return (
        <div class="col-md-6">
          <div class="card text-center shadow-1 hover-shadow-9">
            <div class="card-img-top text-white bg-img h-200 d-flex align-items-center" style={ style } data-overlay="1">
              <TheBgImageUploader name={ imgName } value={ img } setAttributes={ setAttributes } />
              <div class="position-relative w-100">
                <RichText
                  tagName="p"
                  className="lead-4 text-uppercase fw-600 ls-1 mb-0"
                  inlineToolbar
                  onChange={ (v) => this.set(name+'.name', v) }
                  value={ plan.name }
                />
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => this.set(name+'.period', v) }
                  value={ plan.period }
                />
              </div>
            </div>
            <div class="card-body py-6">
              <p class="lead-7 fw-600 text-dark">
                <span class="d-inline-block mr-2">
                  <RichText
                    tagName="span"
                    inlineToolbar
                    onChange={ (v) => this.set(name+'.price', v) }
                    value={ plan.price }
                  />
                </span>

                <span class="d-inline-block">
                  <RichText
                    tagName="span"
                    className="lead-4 align-text-top"
                    inlineToolbar
                    onChange={ (v) => this.set(name+'.price99', v) }
                    value={ plan.price99 }
                  />
                </span>
              </p>
              <RichText
                tagName="p"
                inlineToolbar
                onChange={ (v) => this.set(name+'.text', v) }
                value={ plan.text }
              />
              <br/>
              <div><Button {...props} name={ btnName } /></div>
            </div>
          </div>
        </div>
      );
    }

    return (
      <Section {...props} name="section">
        <div class="row gap-y align-items-center">

          <div class="col-md-4">
            <RichText
              tagName="p"
              className="lead-7 text-dark fw-600 lh-2"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />

            <div class="mt-7">
              <label class="mb-4">
                <input
                  type="checkbox"
                  autocomplete="off"
                  checked={ attributes.cycle.visible }
                  onChange={ () => this.set( 'cycle.visible', ! attributes.cycle.visible )}
                />
                { __('Have monthly and yearly plans?') }
              </label>
            </div>

            <ul class={ "nav nav-tabs-outline justify-content-start mb-7"+ (attributes.cycle.visible ? '' : ' opacity-30') }>
              <li class="nav-item">
                <a class="nav-link w-150 active" data-toggle="tab" href="#price-mo">
                  <RichText
                    tagName="span"
                    inlineToolbar
                    onChange={ (v) => setAttributes({ txtMonthly: v }) }
                    value={ attributes.txtMonthly }
                  />
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link w-150" data-toggle="tab" href="#price-yr">
                  <RichText
                    tagName="span"
                    inlineToolbar
                    onChange={ (v) => setAttributes({ txtYearly: v }) }
                    value={ attributes.txtYearly }
                  />
                </a>
              </li>
            </ul>

            <RichText
              tagName="p"
              className="lead"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />

            <p class="fw-400"><ReadMore {...props} name="link" /></p>
          </div>


          { attributes.cycle.visible === true &&
            <div class="col-md-7 ml-auto">
              <div class="tab-content">
                <div class="tab-pane fade show active" id="price-mo">
                  <div class="row gap-y">
                    { getPlan(1) }
                    { getPlan(2) }
                  </div>
                </div>

                <div class="tab-pane fade" id="price-yr">
                  <div class="row gap-y">
                    { getPlan(1, 'yr') }
                    { getPlan(2, 'yr') }
                  </div>
                </div>
              </div>
            </div>
          }


          { attributes.cycle.visible === false &&
            <div class="col-md-7 ml-auto">
              <div class="row gap-y">
                { getPlan(1) }
                { getPlan(2) }
              </div>
            </div>
          }

        </div>
      </Section>
    );
  }


  set( key, value ) {

    let names   = key.split('.'),
        name    = names.shift(),
        current = this.props.attributes[ name ],
        next    = Object.assign( {}, current );

    next[ names[0] ] = value;
    this.props.setAttributes( { [ name ]: next } );
  }


}
