/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class SocialIcons extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    if ( X.icons === undefined ) {
      return null;
    }

    if ( ! Object.keys(X).length ) {
      return null;
    }

    let classes = classnames( 'social', X.class );

    let icons = Object.entries(X.icons).map(([key, link]) => {
      if ( link === '' ) {
        return;
      }

      key = key.toLowerCase();
      let clas = key;
      let icon = key;
      let data = {};

      if ( icon == 'email' ) {
        icon = 'envelope-o';
      }

      return (<a className={'social-'+ clas} { ...data }><i className={'fa fa-'+ icon}></i></a>);
    })

    let data = {
      'data-show-toolbar': this.props.name
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <div class={classes} {...data}>
          { icons }
        </div>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
