/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Countup extends TheComponent {

  componentDidMount(){
    page.initCountup( $(this.refs.target) );
  }

  componentDidUpdate(prevProps) {
    page.initCountup( $(this.refs.target) );
  }


  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( X.class );

    let data = {
      'data-provide': 'countup',
      'data-from': X.from,
      'data-to': X.to,
      'data-prefix': X.prefix,
      'data-suffix': X.suffix,
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <span class={ classes } {...data} ref="target"></span>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
