
const { __ } = wp.i18n;
const { IconButton } = wp.components;
const { MediaUpload } = wp.editor;


export default function( props ) {
  return (
    <MediaUpload
      onSelect={ (v) => props.setAttributes({ [ props.name ]: v.url }) }
      allowedTypes="image"
      value={ props.value }
      render={ ( { open } ) => (
        <IconButton
          className="components-toolbar__control bgImageUploader"
          label={ __( 'Edit image' ) }
          icon="format-image"
          onClick={ open }
        />
      ) }
    />
  );
}
