/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, SlickSlider } from 'Components';
import { Repeater, TheBgImageUploader } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <SlickSlider name="slider" {...props}>
          <Repeater name="images" {...props}

            content={ (item, set) => (
              <div {...item.data}>
                <div class="bg-img h-600" style={{ backgroundImage: 'url('+ item.img +')', position: 'relative' }}>
                  <TheBgImageUploader name="img" value={ item.img } setAttributes={ set } />
                </div>
              </div>
            )}

          />
        </SlickSlider>
      </Section>
    );
  }

}
