/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { PanelNavbar } from 'Editor';
import * as Util from 'Utils';
import * as config from './config';
import Overlay from './overlay';
import Particles from './particles';
import { default as Panel } from './panel';
import { default as Toolbar } from './toolbar';



export default class Footer extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames( 'footer', {
      'text-white':           X.textWhite,
      'bt-0':                 X.borderTop == false,
      'bg-img':               X.background.type == 'image',
      'bg-fixed':             X.background.imageStyle == 'fixed',
      [ X.paddingTop ]:    !! X.paddingTop,
      [ X.paddingBottom ]: !! X.paddingBottom,
    }, X.class);

    let styles = {
      backgroundColor: X.background.color,
    };

    let data = {
      id: X.id,
    };


    switch( X.background.type ) {
      case 'color':

        break;

      case 'image':
        if ( X.background.imageStyle == 'parallax' ) {
          data['data-parallax'] = X.background.image;
        }
        else {
          styles.backgroundImage = 'url(' + X.background.image + ')';
        }
        break;
    }

    let containerClass = classnames({
      'container':                   X.container === '',
      [ 'container-'+ X.container ]: X.container !== '',
    });


    return (
      <Fragment>
        <Panel { ...this.props } />

        <footer class={ classes } style={ styles } {...data}>
          <Particles options={ X.particles } />
          <Overlay options={ X.overlay } />
          <div class={ containerClass }>
            { this.props.children }
          </div>
        </footer>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
