/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button, Icon } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row text-center">
          <div class="col-md-10 mx-auto">
            <div class="row gap-y">

              <Repeater name="services" {...props}

                content={ (item, set) => (
                  <div class="col-md-6" {...item.data}>
                    <p><Icon {...item.data} name="icon" /></p>
                    <RichText
                      tagName="h5"
                      inlineToolbar
                      onChange={ (v) => set('title', v) }
                      value={ item.title }
                    />
                    <RichText
                      tagName="p"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                  </div>
                )}

              />

            </div>
          </div>
        </div>
      </Section>
    );
  }

}
