/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { PanelNavbar } from 'Editor';
import * as Util from 'Utils';
import * as config from './config';
import { default as Panel } from './panel';



export default class Navbar extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( 'navbar', {
      [`navbar-expand-${X.expand}`]: true,
      [`navbar-${X.color}`]: true,
      [`navbar-stick-${X.stickColor}`]: true,
    }, X.class);

    let containerClass = X.container === '' ? 'container' : 'container-fluid';

    let style = {};
    if ( X.color === "light" ) {
      style.background = '#777';
    }

    return (
      <Fragment>
        <Panel { ...this.props } />

        <nav class={ classes } style={ style }>
          <div class={ containerClass }>
            { this.props.children }
          </div>
        </nav>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
