
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarNav extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const { attributes, setAttributes, name } = this.props;
    const X = this.X();
    const social = X.icons;


    const setSocial = ( media, link ) => {
      let nextSocial = {};
      Object.assign(nextSocial, X.icons);
      nextSocial[ media ] = link;

      setAttributes( { [name +'_icons'] : nextSocial } );
    }

    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>
        <Toolbar>

          <Popup label={ __( 'Social media link' ) } icon="twitter" position="top center">
            <div className="the-popover-more-options">

              <TextControl
                type="url"
                value={ social.facebook }
                onChange={ (v) => setSocial( 'facebook', v ) }
                placeholder="Facebook"
              />

              <TextControl
                type="url"
                value={ social.twitter }
                onChange={ (v) => setSocial( 'twitter', v ) }
                placeholder="Twitter"
              />

              <TextControl
                type="url"
                value={ social.linkedin }
                onChange={ (v) => setSocial( 'linkedin', v ) }
                placeholder="Linkedin"
              />

              <TextControl
                type="url"
                value={ social.instagram }
                onChange={ (v) => setSocial( 'instagram', v ) }
                placeholder="Instagram"
              />

              <TextControl
                type="url"
                value={ social.medium }
                onChange={ (v) => setSocial( 'medium', v ) }
                placeholder="Medium"
              />

              <TextControl
                type="url"
                value={ social.dribbble }
                onChange={ (v) => setSocial( 'dribbble', v ) }
                placeholder="Dribbble"
              />

              <TextControl
                type="url"
                value={ social.youtube }
                onChange={ (v) => setSocial( 'youtube', v ) }
                placeholder="YouTube"
              />

              <TextControl
                type="url"
                value={ social.xing }
                onChange={ (v) => setSocial( 'xing', v ) }
                placeholder="Xing"
              />

              <TextControl
                type="url"
                value={ social.rss }
                onChange={ (v) => setSocial( 'rss', v ) }
                placeholder="RSS"
              />

              <TextControl
                type="url"
                value={ social.email }
                onChange={ (v) => setSocial( 'email', v ) }
                placeholder="Email"
              />

            </div>
          </Popup>

        </Toolbar>
      </TheToolbarContainer>
    );
  }


}
