/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Button, Mailer, Input, GoogleMap } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  componentDidMount(){
    if ( window["google.maps"] === undefined ) {
      jQuery.getScript("https://maps.googleapis.com/maps/api/js?key="+ page.defaults.googleApiKey +"&callback=page.initMap");
    }
    else {
      page.initMap( $(this.refs.target) );
    }
  }

  componentDidUpdate(prevProps) {
    if ( window["google.maps"] === undefined ) {
      page.initMap( $(this.refs.target) );
    }
  }

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row no-gutters">
          <div class="col-md-6 align-self-center py-8">
            <RichText
              tagName="h4"
              className="text-center"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <br/>

            <div class="row">
              <div class="col-11 col-md-10 mx-auto">
                <Mailer {...props} name="mailer">
                  <div class="form-group">
                    <Input {...props} name="inputName" />
                  </div>

                  <div class="form-group">
                    <Input {...props} name="inputEmail" />
                  </div>

                  <div class="form-group">
                    <Input {...props} name="inputMessage" />
                  </div>

                  <Button {...props} name="btn" />
                </Mailer>

              </div>
            </div>
          </div>


          <div class="col-md-6">
            <GoogleMap {...props} name="map" ref="target" />
          </div>
        </div>
      </Section>
    );
  }

}
