
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, Button, Dropdown, ButtonGroup, ColorPalette, TextControl, ToggleControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarIconbox extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();

    //this.set( "content", v )
    return (
      <TheToolbarContainer {...this.props}>
        <Toolbar>

          {/* Icon */}
          <Popup label={ __( 'Icon' ) } icon="image-filter">
            <PickerIcon
              value={ X.content }
              onChange={ (v) => this.set('content', v) }
            />
          </Popup>


          {/* Color */}
          <Popup label={ __( 'Color' ) } icon="art">
            <ColorPalette
              className="the-popover-color-palette"
              colors={ Util.Collection.Colors }
              value={ X.color }
              onChange={ (v) => this.set( 'color', v ) }
            />
          </Popup>


          {/* Background Color */}
          <Popup label={ __( 'Background Color' ) } icon="editor-textcolor">
            <ColorPalette
              className="the-popover-color-palette"
              colors={ Util.Collection.Colors }
              value={ X.backgroundColor }
              onChange={ (v) => this.set( 'backgroundColor', v ) }
            />
          </Popup>


          {/* Size */}
          <Popup label={ __( 'Size' ) } icon="admin-settings">
            <ButtonGroup className="button-group-btn-sizes">
              { Util.Array.Sizes.map(x => (
                <Button key={x} isLarge isPrimary={ X.size === x }
                  onClick={ () => this.set( "size", x ) }
                  >
                  {x.toUpperCase()}
                </Button>
              )) }
              <Button key="xxl" isLarge isPrimary={ X.size === "xxl" }
                onClick={ () => this.set( "size", "xxl" ) }
                >
                XXL
              </Button>
            </ButtonGroup>
          </Popup>

        </Toolbar>
      </TheToolbarContainer>
    );
  }


}
