/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, SocialIcons } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  componentDidMount(){
    if ( window["google.maps"] === undefined ) {
      jQuery.getScript("https://maps.googleapis.com/maps/api/js?key="+ page.defaults.googleApiKey +"&callback=page.initMap");
    }
    else {
      page.initMap( $(this.refs.target) );
    }
  }

  componentDidUpdate(prevProps) {
    if ( window["google.maps"] === undefined ) {
      page.initMap( $(this.refs.target) );
    }
  }

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">
          <div class="col-lg-5 text-center mx-auto">
            <Image {...props} name="imgAuthor" />
            <hr class="d-lg-none my-7"/>
          </div>

          <div class="col-lg-6 align-self-center text-center text-lg-left">
            <RichText
              tagName="h2"
              className="fw-200"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <br/>
            <RichText
              tagName="p"
              inlineToolbar
              multiline
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
            <br/>
            <SocialIcons name="social" {...props} />
            <br/>
          </div>
        </div>
      </Section>
    );
  }

}
