
export default class TheComponent extends React.Component {

  constructor(props) {
    super(props);

    this.X = this.X.bind( this );
    this.set = this.set.bind( this );
    this.setByIndex = this.setByIndex.bind( this );
  }



  X() {
    let { attributes, name, index, parent } = this.props;

    if ( name === undefined ) {
      return attributes;
    }

    if ( index !== undefined && parent !== undefined ) {
      attributes = Object.assign( {}, JSON.parse( JSON.stringify( attributes[ parent ][ index ] ) ) );
    }


    if ( name.indexOf( '.' ) === -1 ) {
      return attributes[ name ];
    }

    let keys = name.split( '.' );
    switch( keys.length ) {
      case 2:
        return attributes[ keys[0] ][ keys[1] ];
        break;

      case 3:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ];
        break;

      case 4:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ];
        break;
    }

    return attributes;
  }



  set( key, value ) {
    // Handle it separately if we set a value for repeater items.
    //
    if ( this.props.index !== undefined && this.props.parent !== undefined ) {
      this.setByIndex( key, value );
      return;
    }

    let names   = this.props.name.split('.'),
        name    = names.shift(),
        current = this.props.attributes[ name ],
        next    = Object.assign( {}, JSON.parse( JSON.stringify( current ) ) ),
        nextArr = [];

    names.push( key );
    key = names.join('.');

    if ( key.indexOf( '.' ) === -1 ) {
      next[ key ] = value;
    }
    else {
      let keys = key.split( '.' );
      switch( keys.length ) {
        case 2:
          next[ keys[0] ][ keys[1] ] = value;
          break;

        case 3:
          next[ keys[0] ][ keys[1] ][ keys[2] ] = value;
          break;

        case 4:
          next[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ] = value;
          break;
      }
    }

    this.props.setAttributes( { [ name ]: next } );
  }


  setByIndex( key, value ) {
    let names   = this.props.name.split('.'),
        //name    = names.shift(),
        parent  = this.props.parent,
        index   = this.props.index,
        current = this.props.attributes[ parent ],
        nextArr = current.slice(0),
        next    = JSON.parse( JSON.stringify( nextArr[ index ] ) );

    names.push( key );
    key = names.join('.');


    //console.log(current, nextArr, next, key);

    if ( key.indexOf( '.' ) === -1 ) {
      next[ key ] = value;
    }
    else {
      let keys = key.split( '.' );
      switch( keys.length ) {
        case 2:
          next[ keys[0] ][ keys[1] ] = value;
          break;

        case 3:
          next[ keys[0] ][ keys[1] ][ keys[2] ] = value;
          break;

        case 4:
          next[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ] = value;
          break;
      }
    }

    nextArr[ index ] = next;
    //console.log(nextArr, index, next);
    this.props.setAttributes( { [ parent ]: nextArr } );

  }



  setRepeaterItem( key, value, index, parent ) {
    if ( typeof key === 'object' ) {
      value = key[ Object.keys(key)[0] ];
      key   = Object.keys(key)[0];
    }

    let current = this.props.attributes[ parent ],
        nextArr = current.slice(0),
        next    = Object.assign( {}, nextArr[ index ] );

    if ( key.indexOf( '.' ) === -1 ) {
      next[ key ] = value;
    }
    else {
      let keys = key.split( '.' );
      switch( keys.length ) {
        case 2:
          next[ keys[0] ][ keys[1] ] = value;
          break;

        case 3:
          next[ keys[0] ][ keys[1] ][ keys[2] ] = value;
          break;

        case 4:
          next[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ] = value;
          break;
      }
    }

    nextArr[ index ] = next;
    this.props.setAttributes( { [ parent ]: nextArr } );

  }

}
