/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">

        <Repeater name="features" {...props}

          content={ (item, set) => (
            <div class="row gap-y align-items-center my-9" {...item.data}>

              <div class="col-md-6">
                <RichText
                  tagName="h4"
                  inlineToolbar
                  onChange={ (v) => set('title', v) }
                  value={ item.title }
                />
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => set('text', v) }
                  value={ item.text }
                />
              </div>

              { ! item.switch &&
                <div class="col-md-5 mr-auto order-md-first">
                  <Image {...item.data} name="img" />
                </div>
              }

              { item.switch &&
                <div class="col-md-5 ml-auto">
                  <Image {...item.data} name="img" />
                </div>
              }
            </div>
          )}

        />

      </Section>
    );
  }

}
