
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton } = wp.components;
const { URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { Popup } from './popup';
import * as Util from 'Utils';



export default class ToolbarRepeaterLink extends TheToolbar {

  render() {

    const { attributes, setAttributes } = this.props;

    return (
      <Toolbar>


        {/* Text */}
        <Popup label={ __( 'Text' ) } icon="edit">
          <input
            className="components-text-control__input rounded-0"
            type="text"
            value={ attributes.text }
            onChange={ (v) => setAttributes('text', v.target.value) }
          />
        </Popup>


        {/* Link */}
        <Popup label={ __( 'Link' ) } icon="admin-links">
          <form
            className="block-library-button__inline-link the-url-input-form"
            onSubmit={ ( event ) => event.preventDefault() }>
            <URLInput
              value={ attributes.url }
              onChange={ (v) => setAttributes('url', v) }
            />
            <IconButton
              className={ 'external-'+ attributes.targetBlank }
              label={ __( 'Open in new tab' ) }
              icon="external"
              onClick={ () => setAttributes('targetBlank', ! attributes.targetBlank) }
            />
          </form>
        </Popup>

      </Toolbar>
    );
  }


}
