/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Shuffle extends TheComponent {

  componentDidMount(){
    page.initShuffle();
  }

  componentDidUpdate(prevProps) {
    page.initShuffle();
  }


  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( X.class );

    let data = {
      'data-provide': 'shuffle',
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <div class={ classes } {...data} ref="target">
          <ul class="nav nav-center nav-bold nav-uppercase nav-slash mb-7" data-shuffle="filter">
            <li class="nav-item">
              <a class="nav-link active" href="#" data-shuffle="button">All</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" data-shuffle="button" data-group="bag">Bag</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" data-shuffle="button" data-group="box">Box</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" data-shuffle="button" data-group="book">Book</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#" data-shuffle="button" data-group="bottle">Bottle</a>
            </li>
          </ul>

          <div class="row gap-y gap-2" data-shuffle="list">

            <div class="col-6 col-lg-3" data-shuffle="item" data-groups="bag,box">
              <a class="portfolio-1" href="#" data-toggle="modal" data-target="#modal-portfolio">
                <img src="../assets/img/portfolio/1.jpg" alt="screenshot" />
                <div class="portfolio-detail">
                  <h5>Phone Bag</h5>
                  <p>Bag, Box</p>
                </div>
              </a>
            </div>

          </div>

        </div>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
