/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';


export default class GoogleMap extends TheComponent {
  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let styles = {
      'height': X.height + 'px',
    }

    let data = {
      'data-provide': "map",
      'data-lat': X.lat,
      'data-lng': X.lng,
      'data-zoom': X.zoom,
      'data-style': X.theme,

      'data-show-toolbar': this.props.name,
    };

    if ( X.marker ) {
      data['marker-lat'] = X.markerLat;
      data['marker-lng'] = X.markerLng;
    }

    return (
      <Fragment>
        <Toolbar {...this.props} />
        <div class={ X.class } { ...data } style={ styles } />
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
