
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, TextControl, Dropdown } = wp.components;
const { URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { Popup } from './popup';
import * as Util from 'Utils';



export default class ToolbarReadMoreLink extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const { attributes, name } = this.props;

    const attr = Util.Attr.Filter( attributes, name +'_' );

    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar>


          {/* Text */}
          <Popup label={ __( 'Text' ) } icon="edit">
            <input
              className="components-text-control__input rounded-0"
              type="text"
              placeholder={ __( 'Type link text' ) }
              value={ attr.text }
              onChange={ (v) => this.set('text', v.target.value) }
            />
          </Popup>


          {/* Link */}
          <Popup label={ __( 'Link' ) } icon="admin-links" position="top center">
            <form
              className="block-library-button__inline-link the-url-input-form"
              onSubmit={ ( event ) => event.preventDefault() }>
              <URLInput
                value={ attr.url }
                onChange={ (v) => this.set('url', v) }
              />
              <IconButton
                className={ 'external-'+ attr.targetBlank }
                label={ __( 'Open in new tab' ) }
                icon="external"
                onClick={ () => this.set('targetBlank', ! attr.targetBlank) }
              />
            </form>
          </Popup>

        </Toolbar>


        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control rotate-90"
                label={ __( 'More options' ) }
                icon="ellipsis"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Extra CSS classes' ) }
                  value={ attr.extraClass }
                  onChange={ (v) => this.set( 'extraClass', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
