/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import * as block_1 from './block-1';
import * as block_2 from './block-2';
import * as block_3 from './block-3';
import * as block_4 from './block-4';
import * as block_5 from './block-5';
import * as block_6 from './block-6';
import * as block_7 from './block-7';
import * as block_8 from './block-8';



/**
 * Register the blocks
 */
const slug = 'explore';
const slugCap = __( slug.charAt(0).toUpperCase() + slug.slice(1) );

[

  block_1,
  block_2,
  block_3,
  block_4,
  block_5,
  block_6,
  block_7,
  block_8,

].forEach( ({ id, settings }) => {
  const name = 'thesaasx/'+ slug +'-'+ id;
  const moreSettings = {
    title: __( slugCap +' '+ id ),
    icon: {
      src: 'screenoptions',
    },
    category: 'thesaasx-'+ slug,
    supports: {
      customClassName : false,
      html: false,
    },
    keywords: [
      __( 'thesaasx' ),
    ],
  };

  Object.assign(settings, moreSettings);
  registerBlockType( name, settings );
});

