/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Icon, Button, Image, ReadMore } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y align-items-center">
          <div class="col-lg-6 mx-auto">
            <RichText
              tagName="h2"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              className="lead mb-7"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />

            <div class="row">
              <div class="col-md-6">
                <Repeater name="featuresLeft" {...props}

                  content={ (item, set) => (
                    <p {...item.data}>
                      <Icon {...item.data} name="icon" />
                      <span className="d-inline-block">
                        <RichText
                          inlineToolbar
                          onChange={ (v) => set('text', v) }
                          value={ item.text }
                        />
                      </span>
                    </p>
                  )}

                />
              </div>

              <div class="col-md-6">
                <Repeater name="featuresRight" {...props}

                  content={ (item, set) => (
                    <p {...item.data}>
                      <Icon {...item.data} name="icon" />
                      <span className="d-inline-block">
                        <RichText
                          inlineToolbar
                          onChange={ (v) => set('text', v) }
                          value={ item.text }
                        />
                      </span>
                    </p>
                  )}

                />
              </div>
            </div>


            <p class="mt-6">
              <ReadMore {...props} name="link" />
            </p>

          </div>

          <div class="col-lg-5 align-self-center order-md-first text-center">
            <Image {...props} name="img" />
          </div>
        </div>
      </Section>
    );
  }

}
