/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Fragment, Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row">

          <Repeater name="services" {...props}

            content={ (item, set) => (
              <Fragment {...item.data}>
                <div class="col-md-5 mr-auto text-md-right">
                  <RichText
                    tagName="h5"
                    inlineToolbar
                    onChange={ (v) => set('title', v) }
                    value={ item.title }
                  />
                  <RichText
                    tagName="p"
                    className="small opacity-50"
                    inlineToolbar
                    onChange={ (v) => set('tags', v) }
                    value={ item.tags }
                  />
                </div>

                <div class="col-md-6">
                  <RichText
                    tagName="p"
                    inlineToolbar
                    onChange={ (v) => set('text', v) }
                    value={ item.text }
                  />
                  <br/><br/>
                </div>
              </Fragment>
            )}

          />

        </div>
      </Section>
    );
  }

}
