/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';



export default class FlashDown extends TheComponent {
  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    if ( X.disable ) {
      return false;
    }

    let classes = classnames( 'scroll-down-'+ X.variation, {
      'scroll-down-white': X.light,
    });

    if ( X.variation == "3" ) {
      return (
      <a className={ classes } href={ '#'+ X.sectionId }>
        <span></span><span></span><span></span>
      </a>
      );
    }

    return (
      <a className={ classes } href={ '#'+ X.sectionId }>
        <span></span>
      </a>
    );
  }


  static get attributes() {
    return config.default;
  }

}
