/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, SocialIcons } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">

        <div class="row">
          <div class="col-10 col-md-7 col-xl-5">
            <div class="section-dialog shadow-4">
              <RichText
                tagName="h5"
                className="fw-500"
                inlineToolbar
                onChange={ (v) => setAttributes({ txtTitle: v }) }
                value={ attributes.txtTitle }
              />
              <blockquote class="blockquote text-left lead-1 mb-0 mt-5">
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => setAttributes({ txtText: v }) }
                  value={ attributes.txtText }
                />
                <RichText
                  tagName="footer"
                  inlineToolbar
                  onChange={ (v) => setAttributes({ txtName: v }) }
                  value={ attributes.txtName }
                />
              </blockquote>
            </div>
          </div>
        </div>

      </Section>
    );
  }

}
