
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl, SelectControl, ToggleControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarForm extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer {...this.props}>

        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Form attributes' ) }
                icon="feedback"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <SelectControl
                  label={ __( 'Method' ) }
                  value={ X.method }
                  onChange={ (v) => { this.set( 'method', v ) } }
                  options={ [
                    { value: 'POST', label: 'Post' },
                    { value: 'GET', label: 'Get' },
                  ] }
                />

                <hr />

                <TextControl
                  label={ __( 'Action Url' ) }
                  value={ X.action }
                  onChange={ (v) => this.set( 'action', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Name' ) }
                  value={ X.name }
                  onChange={ (v) => this.set( 'name', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'ID' ) }
                  value={ X.id }
                  onChange={ (v) => this.set( 'id', v ) }
                />

              </div>
            ) }
          />


          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <ToggleControl
                  label={ __( 'Proceed in new tab' ) }
                  checked={ X.targetBlank }
                  onChange={ (v) => this.set( 'targetBlank', v ) }
                />

                <hr />

                <ToggleControl
                  label={ __( 'Autocomplete' ) }
                  checked={ X.autocomplete }
                  onChange={ (v) => this.set( 'autocomplete', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Extra class' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
