/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;
const { RichText } = wp.editor;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';

import * as config from './config';
import SectionHeader from './header';
import Overlay from './overlay';
import Particles from './particles';
import Video from './video';
import { default as Panel } from './panel';
import { default as Toolbar } from './toolbar';



var prevParticles = "";

export default class Section extends TheComponent {

  componentDidMount(){
    page.initConstellation();
    prevParticles = JSON.stringify( this.props.attributes.section.particles );
  }

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    if ( JSON.stringify( this.props.attributes.section.particles ) !== prevParticles ) {
      page.initConstellation();
      prevParticles = JSON.stringify( this.props.attributes.section.particles );
    }
  }


  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    const Tag = X.isHeaderTag ? 'header' : 'section';

    let classes = classnames(
      {
        'section':              ! X.isHeaderTag,
        'header':               X.isHeaderTag,
        'text-white':           X.textWhite,
        'h-fullscreen':         X.fullscreen,
        'overflow-hidden':      X.overflowHidden,
        'bt-1':                 X.borderTop,
        'bb-1':                 X.borderBottom,
        'bg-fixed':             X.background.imageStyle == 'fixed',
        'bg-size-contain':      X.background.imageStyle == 'contain',
        'section-video':        X.background.type === 'video',
        [ X.paddingTop ]:    !! X.paddingTop,
        [ X.paddingBottom ]: !! X.paddingBottom,
      },
      X.class,
      this.props.className,
    );

    let styles = {
      backgroundColor: X.background.color,
    };

    let data = {
      id: X.id,
    };

    let containerClasses = classnames({
      'container':                   X.container === '',
      [ 'container-'+ X.container ]: X.container !== '',
      'h-100':                       X.fullscreen,
    });

    switch( X.background.type ) {
      case 'color':

        break;

      case 'gradient':
        styles.backgroundImage = X.background.gradient;
        break;

      case 'image':
        if ( X.background.imageStyle == 'parallax' ) {
          data['data-parallax'] = X.background.image;
        }
        else {
          styles.backgroundImage = 'url(' + X.background.image + ')';
        }
        break;

      case 'video':
        if ( X.video.source == 'youtube' ) {
          data['data-video'] = X.video.youtube;
        }
        break;
    }



    return (
      <Fragment>
        <Toolbar { ...this.props } />
        <Panel { ...this.props } />

        <Tag className={ classes } { ...data } style={ styles }>
          <Video options={ X.video } />
          <Particles options={ X.particles } />
          <Overlay options={ X.overlay } />
          <div className={ containerClasses }>
            { X.header.visible &&
              <SectionHeader {...this.props} name="section.header" />
            }
            { this.props.children }
          </div>
        </Tag>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }


}
