/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, SocialIcons } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y">

          <Repeater name="team" {...props}

            content={ (item, set) => (
              <div class="col-md-6 col-lg-3 team-1" {...item.data}>
                <Image {...item.data} name="img" />
                <RichText
                  tagName="h6"
                  inlineToolbar
                  onChange={ (v) => set('name', v) }
                  value={ item.name }
                />
                <RichText
                  tagName="small"
                  inlineToolbar
                  onChange={ (v) => set('position', v) }
                  value={ item.position }
                />
                <RichText
                  tagName="p"
                  inlineToolbar
                  onChange={ (v) => set('about', v) }
                  value={ item.about }
                />
                <SocialIcons {...item.data} name="social" />
              </div>
            )}

          />

        </div>
      </Section>
    );
  }

}
