/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component, Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { Navbar, Button } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    const navbar = attributes.navbar;

    return (
      <Navbar {...props } name="navbar">
        <div class="row">

          <div class="col-8 col-lg-3 navbar-left">
            <a class="navbar-brand">
              <img class="logo-dark" src={ navbar.logoDark } alt="logo" />
              <img class="logo-light" src={ navbar.logoLight } alt="logo" />
            </a>
          </div>

          <section class="col-lg-6 navbar-mobile">
            <nav class="nav nav-navbar mx-auto"><span class="dynamic-placeholder">{ __( 'Navbar Menu' ) }</span></nav>
          </section>

          <div class="col-4 col-lg-3 text-right">

            <div class="navbar_link_icons">
              <nav class="nav nav-navbar">
                {navbar.hideCart === false &&
                  <a class="nav-link" href="#">
                    <i class="fa fa-shopping-cart"></i>
                  </a>
                }
                {navbar.hideSearchIcon === false &&
                  <a class="nav-link" href="#" data-toggle="offcanvas" data-target="#offcanvas-search"><i class="fa fa-search"></i></a>
                }
              </nav>
            </div>

            { ! navbar.hideBtn1 &&
              <Button name="btn1" {...props} />
            }

            { ! navbar.hideBtn2 &&
              <Button name="btn2" {...props} />
            }
          </div>

        </div>
      </Navbar>
    );
  }

}
