/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { TheBgImageUploader } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    const style = {
      backgroundImage: "url("+ attributes.imgBg +")",
      minHeight: "300px",
      position: "relative",
    }

    let section = attributes.section;


    return (
      <Section {...props} name="section">
        <div class="row no-gutters">

          { ! section.switchSides &&
            <div class="col-md-6 bg-img mh-300" style={ style }>
              <TheBgImageUploader name="imgBg" value={ attributes.imgBg } setAttributes={ setAttributes } />
            </div>
          }

          <div class="col-10 col-md-4 mx-auto py-8 text-center text-md-left">
            <RichText
              tagName="p"
              className="small-2 text-light"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtSmall: v }) }
              value={ attributes.txtSmall }
            />
            <RichText
              tagName="h4"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
            <br/>
            <Button {...props} name="btn" />
          </div>

          { section.switchSides &&
            <div class="col-md-6 bg-img mh-300" style={ style }>
              <TheBgImageUploader name="imgBg" value={ attributes.imgBg } setAttributes={ setAttributes } />
            </div>
          }

        </div>
      </Section>
    );
  }

}
