
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl, SelectControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarInput extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer {...this.props}>

        <Toolbar>

          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="feedback"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <SelectControl
                  label={ __( 'Type' ) }
                  value={ X.type }
                  onChange={ (v) => { this.set( 'type', v ) } }
                  options={ [
                    { value: 'text', label: 'Text' },
                    { value: 'email', label: 'Email' },
                    { value: 'password', label: 'Password' },
                    { value: 'url', label: 'Url' },
                    { value: 'tel', label: 'Telephone' },
                    { value: 'textarea', label: 'Textarea' },
                  ] }
                />

                <hr />

                <TextControl
                  label={ __( 'Name' ) }
                  value={ X.name }
                  onChange={ (v) => this.set( 'name', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'ID' ) }
                  value={ X.id }
                  onChange={ (v) => this.set( 'id', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Placeholder' ) }
                  value={ X.placeholder }
                  onChange={ (v) => this.set( 'placeholder', v ) }
                />

              </div>
            ) }
          />


          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Extra class' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
