/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';


export default class Button extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames( 'btn', {
      [`btn-${X.size}`]: X.size !== 'md',
      [ 'btn-' + (X.outline ? 'outline-': '') + X.color ]: true,
      'btn-round':  X.round,
      'btn-circle': X.circle,
      'btn-block':  X.block,
      'btn-glass':  X.glass,
    }, X.class);


    let styles = {
      minWidth: X.minWidth > 0 ? X.minWidth + 'px' : null,
    };


    let data = {
      'type': 'button',
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <button className={ classes } { ...data } style={ styles }>
          { !! X.icon.content && X.icon.position !== "after" &&
            <i className={ X.icon.content +' '+ X.icon.class }></i>
          }
          { X.text }
          { !! X.icon.content && X.icon.position === "after" &&
            <i className={ X.icon.content +' '+ X.icon.class }></i>
          }
        </button>
      </Fragment>
    );
  }



  static get attributes() {
    return config.default;
  }

}
