/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Iconbox extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( 'iconbox', {
      [`iconbox-${X.size}`]: X.size !== 'md',
    }, X.class);

    let style = {
      backgroundColor: X.backgroundColor,
    };


    let iconClasses = classnames({
      [ X.content ] : !! X.content,
    }, X.iconClass);

    let iconStyle = {
      color: X.color,
    };

    let data = {
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <span class={ classes } style={ style } {...data}>
          <i class={ iconClasses } style={ iconStyle }></i>
        </span>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
