/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y text-center">
          <Repeater name="cards" {...props}

            content={ (item, set) => (
              <div class="col-lg-4" {...item.data}>
                <div class="card border p-5">
                  <RichText
                    tagName="h5"
                    className="mb-4"
                    inlineToolbar
                    onChange={ (v) => set('title', v) }
                    value={ item.title }
                  />
                  <RichText
                    tagName="p"
                    className="small-1"
                    inlineToolbar
                    onChange={ (v) => set('text', v) }
                    value={ item.text }
                  />
                  <RichText
                    tagName="div"
                    inlineToolbar
                    onChange={ (v) => set('link', v) }
                    value={ item.link }
                  />
                </div>
              </div>
            )}

          />
        </div>
      </Section>
    );
  }

}
