
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton, Dropdown, SelectControl, TextControl, RangeControl } = wp.components;


/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarRating extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const { attributes, name } = this.props;
    const X = this.X();


    return (
      <TheToolbarContainer {...this.props} index={ this.props.index }>

        <Toolbar>

          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Settings' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <RangeControl
                  label={ __( 'Stars' ) }
                  value={ X.value }
                  onChange={ (v) => this.set( 'value', v ) }
                  min={ 0 }
                  max={ 5 }
                  step={ 0.5 }
                />

                <hr />

                <TextControl
                  label={ __( 'Extra classes' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />

        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
