/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { InnerBlocks } = wp.editor;

/**
 * Internal dependencies
 */
import * as Util from 'Utils';
import * as config_contact_8 from './../../contact/block-8/config.json';
import * as config_contact_9 from './../../contact/block-9/config.json';
import * as config_map_1 from './../../map/block-1/config.json';



const template = [
  [ 'thesaasx/contact-9', Util.Attr.ParseForTemplate( config_contact_9, {
    section: {
      background: {
        color: '#f9fafa'
      }
    }
  })],
  [ 'thesaasx/map-1', Util.Attr.ParseForTemplate( config_map_1, {
    map: {
      height: 400
    }
  })],
  [ 'thesaasx/contact-8', Util.Attr.ParseForTemplate( config_contact_8, {
    section: {
      background: {
        color: '#f9fafa'
      }
    }
  })],
];


/**
 * Block definition
 */
export const id = 'contact-3';
export const settings = {
  title: __('Page — Contact 3'),
  edit() { return <InnerBlocks template={ template } templateLock={ false } /> },
  save() { return <InnerBlocks.Content /> }
};
