/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button, Form, Input, Icon } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <RichText
          tagName="h2"
          className="text-center"
          inlineToolbar
          onChange={ (v) => setAttributes({ txtTitle: v }) }
          value={ attributes.txtTitle }
        />
        <RichText
          tagName="p"
          className="text-center opacity-70 lead"
          inlineToolbar
          onChange={ (v) => setAttributes({ txtText: v }) }
          value={ attributes.txtText }
        />

        <br/>

        <div class="row">
          <Form {...props} name="form">
            <div class="form-group input-group input-group-lg">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <Icon {...props} name="iconName" />
                </span>
              </div>
              <Input {...props} name="inputName" />
            </div>

            <div class="form-group input-group input-group-lg">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <Icon {...props} name="iconEmail" />
                </span>
              </div>
              <Input {...props} name="inputEmail" />
            </div>

            <div class="form-group input-group input-group-lg">
              <div class="input-group-prepend">
                <span class="input-group-text">
                  <Icon {...props} name="iconPass" />
                </span>
              </div>
              <Input {...props} name="inputPass" />
            </div>

            <Button {...props} name="btn" />
          </Form>
        </div>

      </Section>
    );
  }

}
