
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, IconButton, TextControl, RangeControl } = wp.components;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarCountup extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer name={ this.props.name } index={ this.props.index }>

        <Toolbar>
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-settings"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'From (number)' ) }
                  value={ X.from }
                  onChange={ (v) => this.set( 'from', v ) }
                />

                <hr/>

                <TextControl
                  label={ __( 'To (number)' ) }
                  value={ X.to }
                  onChange={ (v) => this.set( 'to', v ) }
                />

                <hr/>

                <TextControl
                  label={ __( 'Prefix' ) }
                  value={ X.prefix }
                  onChange={ (v) => this.set( 'prefix', v ) }
                />

                <hr/>

                <TextControl
                  label={ __( 'Suffix' ) }
                  value={ X.suffix }
                  onChange={ (v) => this.set( 'suffix', v ) }
                />

              </div>
            ) }
          />

          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <TextControl
                  label={ __( 'Extra CSS classes' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
