/**
 * External dependencies
 */

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;
const { PanelBody, ToggleControl, Button, ButtonGroup, ColorPalette, SelectControl, RangeControl, TextControl } = wp.components;
const { InspectorControls, MediaUpload } = wp.editor;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { Row, PickerGradient, TheColorPalette } from 'Editor';
import * as Util from 'Utils';



export default class PanelNavbar extends TheComponent {

  render() {
    const X = this.props.attributes[ this.props.name ];


    return (
      <InspectorControls>

        <PanelBody title={ __( 'Navbar Settings' ) } initialOpen={ true }>


          <SelectControl
            label={ __( 'Type' ) }
            value={ X.mode }
            onChange={ (v) => this.set( 'mode', v ) }
            options={[
              { value: 'static', label: __( 'Static' ) },
              { value: 'fixed',  label: __( 'Fixed' ) },
              { value: 'sticky', label: __( 'Sticky' ) },
              { value: 'smart',  label: __( 'Smart' ) },
            ]}
          />

          <hr />

          <SelectControl
            label={ __( 'Default color' ) }
            value={ X.color }
            onChange={ (v) => this.set( 'color', v ) }
            options={[
              { value: 'dark',  label: __( 'Dark' ) },
              { value: 'light', label: __( 'Light' ) },
            ]}
          />

          <SelectControl
            label={ __( 'Color on sticky' ) }
            value={ X.stickColor }
            onChange={ (v) => this.set( 'stickColor', v ) }
            options={[
              { value: 'dark',  label: __( 'Dark' ) },
              { value: 'light', label: __( 'Light' ) },
            ]}
          />

          <hr />

          <SelectControl
            label={ __( 'Expands on' ) }
            value={ X.expand }
            onChange={ (v) => this.set( 'expand', v ) }
            options={[
              { value: 'sm', label: __( 'Phones' ) },
              { value: 'md', label: __( 'Tablets' ) },
              { value: 'lg', label: __( 'Desktops' ) },
              { value: 'xl', label: __( 'Large desktops' ) },
            ]}
          />

          <SelectControl
            label={ __( 'Container' ) }
            value={ X.container }
            onChange={ (v) => this.set( 'container', v ) }
            options={[
              { value: '',      label: __( 'Default' ) },
              { value: 'fluid', label: __( 'Fluid' ) },
            ]}
          />

        </PanelBody>


        <PanelBody title={ __( 'Logo' ) } initialOpen={ false }>

          <label class="components-base-control__label">{ __( 'Dark colored' ) }</label>

          <MediaUpload
            onSelect={ (v) => this.set( 'logoDark', v.url ) }
            type="image"
            value={ X.logoDark }
            render={ ( { open } ) => (
              <div>
                <div className="the-media-upload" onClick={ open }>
                  { ! X.logoDark &&
                    <span>{ __( 'Choose an image' ) }</span>
                  }
                  { !! X.logoDark &&
                    <img src={ X.logoDark } />
                  }
                </div>
                { !! X.logoDark &&
                  <Button onClick={ () => this.set( 'logoDark', '' ) } isLink isDestructive>{ __( 'Remove image' ) }</Button>
                }
              </div>
            ) }
          />

          <hr />

          <label class="components-base-control__label">{ __( 'Light colored' ) }</label>

          <MediaUpload
            onSelect={ (v) => this.set( 'logoLight', v.url ) }
            type="image"
            value={ X.logoLight }
            render={ ( { open } ) => (
              <div>
                <div className="the-media-upload" style={{background: '#777'}} onClick={ open }>
                  { ! X.logoLight &&
                    <span>{ __( 'Choose an image' ) }</span>
                  }
                  { !! X.logoLight &&
                    <img src={ X.logoLight } />
                  }
                </div>
                { !! X.logoLight &&
                  <Button onClick={ () => this.set( 'logoLight', '' ) } isLink isDestructive>{ __( 'Remove image' ) }</Button>
                }
              </div>
            ) }
          />

        </PanelBody>



        <PanelBody title={ __( 'Buttons' ) } initialOpen={ false }>


          <ToggleControl
            label={ __( 'Hide button 1' ) }
            checked={ X.hideBtn1 }
            onChange={ (v) => this.set( 'hideBtn1', v ) }
          />


          <ToggleControl
            label={ __( 'Hide button 2' ) }
            checked={ X.hideBtn2 }
            onChange={ (v) => this.set( 'hideBtn2', v ) }
          />


          <ToggleControl
            label={ __( 'Hide Cart Icon' ) }
            checked={ X.hideCart }
            onChange={ (v) => this.set( 'hideCart', v ) }
          />

          <ToggleControl
            label={ __( 'Hide Search Icon' ) }
            checked={ X.hideSearchIcon }
            onChange={ (v) => this.set( 'hideSearchIcon', v ) }
          />


        </PanelBody>

      </InspectorControls>
    );
  }

}

