/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };


    return (
      <Section {...props} name="section">
        <div class="row align-items-center">

          <div class="col-lg-6">
            <div class="video-btn-wrapper">
              <Image {...props} name="imgCover" />
              <Button {...props} name="btnPlay" />
            </div>
          </div>

          <div class="col-lg-6">
            <ol class="step">

              <Repeater name="steps" {...props}

                content={ (item, set) => (
                  <li class="step-item" {...item.data}>
                    <div class="step-icon">
                      <RichText
                        tagName="span"
                        className="iconbox"
                        inlineToolbar
                        onChange={ (v) => set('number', v) }
                        value={ item.number }
                      />
                    </div>

                    <div class="step-content">
                      <RichText
                        tagName="h6"
                        className="fw-500"
                        inlineToolbar
                        onChange={ (v) => set('title', v) }
                        value={ item.title }
                      />
                      <RichText
                        tagName="p"
                        inlineToolbar
                        onChange={ (v) => set('text', v) }
                        value={ item.text }
                      />
                    </div>
                  </li>
                )}

              />

            </ol>
          </div>

        </div>
      </Section>
    );
  }

}
