/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y">
          <div class="col-md-3 mr-md-auto">
            <RichText
              tagName="p"
              className="lead-4 text-right"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtLead: v }) }
              value={ attributes.txtLead }
            />
          </div>

          <div class="col-md-4">
            <RichText
              tagName="h6"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle1: v }) }
              value={ attributes.txtTitle1 }
            />
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText1: v }) }
              value={ attributes.txtText1 }
            />
          </div>

          <div class="col-md-4">
            <RichText
              tagName="h6"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle2: v }) }
              value={ attributes.txtTitle2 }
            />
            <RichText
              tagName="p"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText2: v }) }
              value={ attributes.txtText2 }
            />
          </div>
        </div>
      </Section>
    );
  }

}
