/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class SlickSlider extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    let classes = classnames( 'slider-items-wrapper', 'slides-num-'+ X.slidesToShow );

    return (
      <Fragment>
        <Toolbar {...this.props} />

        <div class={ classes } data-show-toolbar={ this.props.name }>
          { this.props.children }
        </div>
      </Fragment>
    );
  }

  static get attributes() {
    return config.default;
  }

}
