/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Countup, Icon } from 'Components';
import { Repeater } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row gap-y">

          <Repeater name="numbers" {...props}

            content={ (item, set) => (
              <div class="col-lg-4" { ...item.data }>
                <div class="row">
                  <div class="col-6">
                    <h4 class="lead-7 text-right">
                      <Countup {...item.data} name="countup" />
                    </h4>
                  </div>

                  <div class="col-6">
                    <RichText
                      tagName="p"
                      className="small text-uppercase ls-2 mb-2"
                      inlineToolbar
                      onChange={ (v) => set('text', v) }
                      value={ item.text }
                    />
                    <p><Icon {...item.data} name="icon" /></p>
                  </div>
                </div>
              </div>
            )}

          />

        </div>
      </Section>
    );
  }

}
