/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

/**
 * Internal dependencies
 */
import * as about_1 from './about-1';
import * as about_2 from './about-2';
import * as about_3 from './about-3';
import * as contact_1 from './contact-1';
import * as contact_2 from './contact-2';
import * as contact_3 from './contact-3';
import * as hiw_1 from './how-it-works-1';
import * as job_1 from './job-1';
import * as portfolio_1 from './portfolio-1';
import * as pricing_1 from './pricing-1';
import * as service_1 from './service-1';
import * as service_2 from './service-2';



/**
 * Register the blocks
 */
const slug = 'page';
const slugCap = __( slug.charAt(0).toUpperCase() + slug.slice(1) );

[

  about_1,
  about_2,
  about_3,
  contact_1,
  contact_2,
  contact_3,
  hiw_1,
  job_1,
  portfolio_1,
  pricing_1,
  service_1,
  service_2,

].forEach( ({ id, settings }) => {
  const name = 'thesaasx/'+ slug +'-'+ id;
  const moreSettings = {
    title: settings.title,
    icon: {
      src: 'media-document',
    },
    category: 'thesaasx-'+ slug,
    supports: {
      customClassName : false,
      html: false,
    },
    keywords: [
      __( 'thesaasx' ),
    ],
  };

  Object.assign(settings, moreSettings);
  registerBlockType( name, settings );
});

