/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Card extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();

    const Tag = X.url === "" ? 'div' : 'a';

    let classes = classnames( 'card', {
      'text-white':  X.textWhite,
    }, X.class );

    let styles = {
      backgroundColor: X.background.color === '' ? '' : X.background.color,
    };

    let data = {
      'data-show-toolbar': this.props.name,
    };

    switch( X.background.type ) {
      case 'color':

        break;

      case 'gradient':
        styles.backgroundImage = X.background.gradient;
        break;

      case 'image':
        styles.backgroundImage = 'url(' + X.background.image + ')';
        break;
    }

    return (
      <Fragment>
        <Toolbar {...this.props} />

        <Tag className={ classes } {...data} style={ styles }>
          <Overlay {...this.props} options={ X.overlay } />
        </Tag>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}



function Overlay( props ) {
  const X = props.options;

  if ( X.opacity == 0 ) {
    return props.children;
  }

  let classes = classnames( "overlay", "opacity-"+ X.opacity );
  let styles = {};

  if ( X.type === 'color' ) {
    styles['backgroundColor'] = X.color;
  }
  else if ( X.type === 'gradient' ) {
    styles['backgroundImage'] = X.gradient;
  }

  return (
    <Fragment>
      <div class={ classes } style={ styles }></div>
      <div class="position-relative">
        { props.children }
      </div>
    </Fragment>
  );
}
