
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, IconButton } = wp.components;
const { MediaUpload, URLInput } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Toolbar';
import { Popup } from './popup';
import * as Util from 'Utils';


const ALLOWED_MEDIA_TYPES = [ 'image', 'video' ];
const IMAGE_BACKGROUND_TYPE = 'image';
const VIDEO_BACKGROUND_TYPE = 'video';



export default class ToolbarRepeaterImage extends TheToolbar {

  render() {

    const { attributes, setAttributes } = this.props;

    const onSelectMedia = ( media ) => {
      if ( ! media || ! media.url ) {
        return;
      }

      setAttributes('img_src', media.url);
    }


    return (
      <Toolbar>

        <MediaUpload
          onSelect={ onSelectMedia }
          allowedTypes="image"
          value={ attributes.img_src }
          render={ ( { open } ) => (
            <IconButton
              className="components-toolbar__control"
              label={ __( 'Change image' ) }
              icon="format-image"
              onClick={ open }
            />
          ) }
        />


        {/* Alt Text */}
        <Popup label={ __( 'Image alt text' ) } icon="edit">
          <input
            className="components-text-control__input rounded-0"
            type="text"
            value={ attributes.img_alt }
            onChange={ (v) => setAttributes('img_alt', v.target.value) }
          />
        </Popup>


        {/* Link */}
        <Popup label={ __( 'Link' ) } icon="admin-links">
          <form
            className="block-library-button__inline-link the-url-input-form"
            onSubmit={ ( event ) => event.preventDefault() }>
            <URLInput
              value={ attributes.img_url }
              onChange={ (v) => setAttributes('img_url', v) }
            />
            <IconButton
              className={ 'external-'+ attributes.img_targetBlank }
              label={ __( 'Open in new tab' ) }
              icon="external"
              onClick={ () => setAttributes('img_targetBlank', ! attributes.img_targetBlank) }
            />
          </form>
        </Popup>

      </Toolbar>
    );
  }


}
