/**
 * External dependencies
 */

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Fragment } = wp.element;
const { PanelBody, ToggleControl, Button, ButtonGroup, ColorPalette, SelectControl, RangeControl, TextControl } = wp.components;
const { InspectorControls, MediaUpload } = wp.editor;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import { Row, PickerGradient, TheColorPalette } from 'Editor';
import * as Util from 'Utils';



export default class PanelFooter extends TheComponent {

  render() {
    const X = this.props.attributes[ this.props.name ];

    return (
      <InspectorControls>

        <PanelBody title={ __( 'Section Settings' ) } initialOpen={ true }>

          <TheColorPalette
            label="Background color"
            value={ X.background.color }
            onChange={ (v) => this.set( 'background.color', v ) }
          />

          <hr />

          <ToggleControl
            label={ __( 'White text color' ) }
            checked={ X.textWhite }
            onChange={ (v) => this.set( 'textWhite', v ) }
          />

          <hr />

          <ToggleControl
            label={ __( 'Border top' ) }
            checked={ X.borderTop }
            onChange={ (v) => this.set( 'borderTop', v ) }
          />

          { X.switchable &&
            <Fragment>
              <hr />

              <ToggleControl
                label={ __( 'Switch sides' ) }
                checked={ X.switchSides }
                onChange={ (v) => this.set( 'switchSides', v ) }
              />
            </Fragment>
          }

        </PanelBody>



        <PanelBody title={ __( 'Background Type' ) } initialOpen={ false }>

          <SelectControl
            value={ X.background.type }
            onChange={ (v) => this.set( 'background.type', v ) }
            options={[
              { value: 'color',    label: __( 'Color' ) },
              { value: 'image',    label: __( 'Image' ) },
            ]}
          />


          <div className={ X.background.type === "image" ? "" : "d-none" }>
            <hr />

            <MediaUpload
              onSelect={ (v) => this.set( 'background.image', v.url ) }
              type="image"
              value={ X.background.image }
              render={ ( { open } ) => (
                <div>
                  <div className="the-media-upload" onClick={ open }>
                    { ! X.background.image &&
                      <span>{ __( 'Choose an image' ) }</span>
                    }
                    { !! X.background.image &&
                      <img src={ X.background.image } />
                    }
                  </div>
                  { !! X.background.image &&
                    <Button onClick={ () => this.set( 'background.image', '' ) } isLink isDestructive>{ __( 'Remove image' ) }</Button>
                  }
                </div>
              ) }
            />

            <hr />

            <SelectControl
              label={ __( 'Display style' ) }
              value={ X.background.imageStyle }
              onChange={ (v) => this.set( 'background.imageStyle', v ) }
              options={ [
                { value: 'none',     label: __( 'None' ) },
                { value: 'fixed',    label: __( 'Fixed' ) },
              ] }
            />
          </div>

        </PanelBody>


        <PanelBody title={ __( 'Overlay' ) } initialOpen={ false }>

          <RangeControl
            label={ __( 'Overlay opacity' ) }
            value={ X.overlay.opacity }
            onChange={ (v) => this.set( 'overlay.opacity', v ) }
            min={ 0 }
            max={ 100 }
            step= { 5 }
          />

          <hr />

          <SelectControl
            label={ __( 'Overlay color type' ) }
            value={ X.overlay.type }
            onChange={ (v) => this.set( 'overlay.type', v ) }
            options={ [
              { value: 'color',    label: __( 'Solid color' ) },
              { value: 'gradient', label: __( 'Gradient' ) },
            ] }
          />

          <hr />

          <div className={ X.overlay.type === "color" ? "" : "d-none" }>
            <TheColorPalette
              label={ __( 'Overlay color' ) }
              value={ X.overlayColor }
              onChange={ (v) => this.set( 'overlay.color', v ) }
            />
          </div>

          <div className={ X.overlay.type === "gradient" ? "" : "d-none" }>
            <PickerGradient
              value={ X.overlayGradient }
              onChange={ (v) => this.set( 'overlay.gradient', v ) }
            />
          </div>

        </PanelBody>


        <PanelBody title={ __( 'Particles' ) } initialOpen={ false }>

          <ToggleControl
            label={ __( 'Enable particles' ) }
            checked={ X.particles.visible }
            onChange={ (v) => this.set( 'particles.visible', v ) }
          />

          <div className={ X.particles ? "" : "d-none" }>
            <hr />
            <TheColorPalette
              label={ __( 'Color' ) }
              value={ X.particles.color }
              onChange={ (v) => this.set( 'particles.color', v ) }
            />

            <hr />

            <RangeControl
              label={ __( 'Length' ) }
              value={ X.particles.length }
              onChange={ (v) => this.set( 'particles.length', v ) }
              min={ 0 }
              max={ 500 }
              step= { 5 }
            />

            <hr />

            <RangeControl
              label={ __( 'Radius' ) }
              value={ X.particles.radius }
              onChange={ (v) => this.set( 'particles.radius', v ) }
              min={ 0 }
              max={ 500 }
              step= { 5 }
            />
          </div>

        </PanelBody>


        <PanelBody title={ __( 'Sizing' ) } initialOpen={ false }>

          <Row>
            <SelectControl
              label={ __( 'Padding top' ) }
              value={ X.paddingTop }
              onChange={ (v) => this.set( 'paddingTop', v ) }
              options={ [
                { value: '',      label: __( 'Default' ) },
                { value: 'pt-0',  label: __( 'None' ) },
                { value: 'pt-3',  label: __( 'Extra small' ) },
                { value: 'pt-5',  label: __( 'Small' ) },
                { value: 'pt-7',  label: __( 'Medium' ) },
                { value: 'pt-9',  label: __( 'Large' ) },
                { value: 'pt-11', label: __( 'Extra large' ) },
                { value: 'pt-13', label: __( 'Extra extra large' ) },
              ] }
            />

            <SelectControl
              label={ __( 'Padding bottom' ) }
              value={ X.paddingBottom }
              onChange={ (v) => this.set( 'paddingBottom', v ) }
              options={ [
                { value: '',      label: __( 'Default' ) },
                { value: 'pb-0',  label: __( 'None' ) },
                { value: 'pb-3',  label: __( 'Extra small' ) },
                { value: 'pb-5',  label: __( 'Small' ) },
                { value: 'pb-7',  label: __( 'Medium' ) },
                { value: 'pb-9',  label: __( 'Large' ) },
                { value: 'pb-11', label: __( 'Extra large' ) },
                { value: 'pb-13', label: __( 'Extra extra large' ) },
              ] }
            />
          </Row>

        </PanelBody>


        <PanelBody title={ __( 'Advanced' ) } initialOpen={ false }>

          <TextControl
            label={ __( 'HTML Anchor (ID)' ) }
            value={ X.id }
            onChange={ (v) => this.set( 'id', v ) }
          />

          <hr />

          <TextControl
            label={ __( 'Additional CSS Class' ) }
            value={ X.class }
            onChange={ (v) => this.set( 'class', v ) }
          />

        </PanelBody>

      </InspectorControls>
    );
  }

}
