/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Mailer extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames(
      X.class
    );

    let styles = {

    };

    let data = {
      'data-form': 'mailer',
      'data-show-toolbar': this.props.name,
    };


    return (
      <Fragment>
        <Toolbar {...this.props} />

        <form class={ classes } {...data}>
          <input type="hidden" name="success-message" value={ X.message } />
          <div class="alert alert-success d-on-success">{ X.message }</div>
          { this.props.children }
        </form>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
