
const { __ } = wp.i18n;
import {
  Section,
  Button,
  Image,
  Icon,
  Iconbox,
  Input,
  ReadMore,
  AppLink,
  FlashDown,
  GoogleMap,
  SocialIcons,
  SlickSlider,
  Navbar,
  Mailer,
  Countup,
  Shuffle,
  Form,
  Card,
  Footer,
  Nav,
  Rating,
} from 'Components';


/**
 * Attributes utility class
 */
export class Attr {


  /**
   * Parse config.json content into block attributes object.
   */
  static Parse( json ) {
    let output = {};

    const components = {
      'Section': Section,
      'Button': Button,
      'Image': Image,
      'Icon': Icon,
      'Iconbox': Iconbox,
      'Input': Input,
      'ReadMore': ReadMore,
      'AppLink': AppLink,
      'FlashDown': FlashDown,
      'GoogleMap': GoogleMap,
      'SocialIcons': SocialIcons,
      'SlickSlider': SlickSlider,
      'Navbar': Navbar,
      'Mailer': Mailer,
      'Countup': Countup,
      'Shuffle': Shuffle,
      'Form': Form,
      'Card': Card,
      'Footer': Footer,
      'Nav': Nav,
      'Rating': Rating,
    };


    Object.entries( json.default ).forEach( ( [ name, obj ] ) => {

      // Check if it's a component or a simple variable.
      //
      if ( obj.type === undefined ) {
        let type = typeof obj;

        if ( Object.prototype.toString.call( obj ) == '[object Array]' ) {
          type = "array";

          obj.forEach( element => {
            Object.entries( element ).forEach( ( [ key, val ] ) => {
              if ( typeof val !== 'object' ) {
                return;
              }

              if ( val.type === undefined || components[ val.type ] === undefined ) {
                return;
              }

              const comp   = components[ val.type ];
              let compAttr = Object.assign( {}, comp['attributes'] );
              delete val.type;
              if ( val.default !== undefined ) {
                const defaults = val.default;
                delete val.default;
                Object.entries( defaults ).forEach( ( [ key, val ] ) => {
                  if ( key in compAttr ) {
                    if ( typeof val === 'object' ) {
                      compAttr[ key ] = Attr._replaceDeepValues( compAttr[ key ], val );
                    }
                    else {
                      compAttr[ key ] = val;
                    }
                  }
                });
              }

              delete val.default;
              Object.assign( val, compAttr );

            });
          });
        }

        let newObj = {
          type: type,
          default: obj
        }
        output[ name ] = newObj;
      }
      else {
        if ( components[ obj.type ] === undefined ) {
          return;
        }

        const comp   = components[ obj.type ];
        let compAttr = Object.assign( {}, comp.attributes );

        if ( obj.default !== undefined && compAttr !== undefined ) {
          Object.entries( obj.default ).forEach( ( [ key, val ] ) => {
            if ( key in compAttr ) {
              if ( typeof val === 'object' ) {
                compAttr[ key ] = Attr._replaceDeepValues( compAttr[ key ], val );
              }
              else {
                compAttr[ key ] = val;
              }
            }
          });
        }

        output[ name ] = {
          type: 'object',
          default: compAttr
        }
      }
    });

    output = Attr.RefineConfigValue( output );

    return output;
  }



  /**
   * Parse config.json content into block attributes for template blocks.
   */
  static ParseForTemplate( json, defaults ) {
    const parsed = JSON.parse( JSON.stringify( Attr.Parse(json) ) );
    let output = {};
    defaults = Attr.RefineConfigValue( defaults );

    Object.entries( parsed ).forEach( ( [ key, val ] ) => {
      if ( val.default === undefined ) {
        output[ key ] = val;
      }
      else {
        output[ key ] = val.default;
      }
    });

    return mergeDeep( output, defaults );
  }



  static _replaceDeepValues( attributes, config ) {
    let output = Object.assign( {}, attributes );

    Object.entries( config ).forEach( ( [ key, val ] ) => {
      if ( typeof val === 'object' ) {
        output[ key ] = Attr._replaceDeepValues( output[ key ], val );
      }
      else {
        output[ key ] = val;
      }
    });

    return output;
  }


  /**
   * Add some data to default values based on the string metas.
   */
  static RefineConfigValue( value ) {
    if ( value !== null && typeof value === 'object' ) {
      Object.entries( value ).forEach( ( [ key, val ] ) => {
        value[ key ] =  Attr.RefineConfigValue( val )
      });
    }
    else if ( typeof value === 'string' && value.indexOf( '(img)' ) === 0 ) {
      return Url.Img( value.substring(6) );
    }

    return value;
  }


  /**
   * Get an attribute object with name.
   */
  static Get( attributes, name ) {
    if ( name === undefined ) {
      return attributes;
    }

    if ( name.indexOf( '.' ) === -1 ) {
      return attributes[ name ];
    }

    let keys = name.split( '.' );
    switch( keys.length ) {
      case 2:
        return attributes[ keys[0] ][ keys[1] ];
        break;

      case 3:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ];
        break;

      case 4:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ];
        break;
    }

    return attributes;
  }


  /**
   * Set an attribute value with key.
   */
  static Set( attributes, key, value ) {
    if ( key.indexOf( '.' ) === -1 ) {
      attributes[ key ] = value;
    }
    else {
      let keys = key.split( '.' );
      switch( keys.length ) {
        case 2:
          attributes[ keys[0] ][ keys[1] ] = value;
          break;

        case 3:
          attributes[ keys[0] ][ keys[1] ][ keys[2] ] = value;
          break;

        case 4:
          attributes[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ] = value;
          break;
      }
    }

    return attributes;
  }


  /**
   * Force Gutenberg to save all default values inside database.
   */
  static SetDefaults( attributes, blockAttributes ) {
    if ( attributes.block_init ) {
      return attributes;
    }

    let defaults = {};
    const entries = Object.entries(blockAttributes);
    for (const [attr, key] of entries) {
      if ( key.value !== undefined ) {
        defaults[ attr ] = key.value;
      }
    }

    defaults['block_init'] = true;
    Object.assign(attributes, defaults);

    return attributes;
  }


  /**
   * Get block attributes from different components and join them together.
   */
  static Merge( groups, extra = {} ) {
    let attributes = [];
    const classesMapping = {
      'Section': Section,
      'Button': Button,
    };

    for ( let group of groups ) {

      if ( typeof group.type === 'string' ) {
        if ( classesMapping[ group.type ] !== undefined ) {
          group.type = classesMapping[ group.type ];
        }
        else {
          console.log('Error in class mapping.');
          continue;
        }
      }

      let prefix = group.name + '_';
      let source = group.type['attributes'];
      let values = group.values;

      if ( values !== undefined ) {
        Object.entries(values).forEach(([key, val]) => {
          if ( key in source ) {
            source[key]['value'] = val;
            //source[key]['default'] = val;
          }
        });
      }

      let new_source = Object.assign(...Object.entries( source ).map( ([k, v]) => ( {[prefix+k]: v} ) ));

      attributes.push( new_source );
    }

    let moreExtra = {
      block_init: {
        type: 'boolean',
        value: false,
      }
    }

    return Object.assign( {}, ...attributes, extra, moreExtra );
  }


  /**
   * Filter block attributes based on a prefix value.
   */
  static Filter( source, prefix ) {
    if ( source === undefined ) {
      return source;
    }

    var keys = Object.keys( source ).filter( v => v.startsWith( prefix ) );
    var obj = {};

    for ( var key of keys ) {
      obj[ key.replace( prefix, '' ) ] = source[ key ];
    }

    return obj;
  }

}


