/**
 * Url utility class
 */
export class Url {

  static Img( file ) {
    return wp_php.theme_uri +'/assets/img/'+ file;
  }

}




/**
 * Arrays — Single values for .map purpose
 */
export class Array {

  static get Sizes() {
    return [ 'xs', 'sm', 'md', 'lg', 'xl' ];
  }

}


/**
 * Collections
 */
export class Collection {

  static get Sizes() {
    return [
      { value: 'xs', label: __( 'Extra small' ) },
      { value: 'sm', label: __( 'Small' ) },
      { value: 'md', label: __( 'Normal' ) },
      { value: 'lg', label: __( 'Large' ) },
      { value: 'xl', label: __( 'Extra large' ) },
    ];
  }

  static get ColorNames() {
    return [
      { value: 'primary',   label: __( 'Primary' ) },
      { value: 'secondary', label: __( 'Secondary' ) },
    ];
  }

  static get Colors() {
    return [
      { name: 'Primary',   color: wp_php.colorPrimary },
      { name: 'Secondary', color: wp_php.colorSecondary },
      { name: 'Success',   color: '#78cf18' },
      { name: 'Danger',    color: '#ff4954' },
      { name: 'Warning',   color: '#fb8302' },
      { name: 'Info',      color: '#7a54d8' },
      { name: 'Light',     color: '#f9fafa' },
      { name: 'Dark',      color: '#191919' },
    ];
  }

  static get BackgroundColors() {
    return [
      { name: 'White', color: '#fff' },
      { name: 'Gray', color: '#f9fafa' },
      { name: 'Dark', color: '#191919' },
    ];
  }

}


/**
 * Placeholder text as default value. Something like Lorem Ipsum
 */
export class Placeholder {

  static Feature( fields=[ 'title', 'text' ], total=3 ) {
    let lorem = [],
        ipsum = {};

    const bank = {
      title: __( 'Feature title' ),
      text:  __( 'Some description about the feature' ),
      avatar: '/assets/img/avatar.jpg',
      image:  '/assets/img/image.jpg',
      img_src:  '/assets/img/image.jpg',
      img_alt:  __( 'Alt image' ),
    }

    if ( fields.length === 0 ) {
      ipsum = bank;
    }
    else {
      fields.map( x => ipsum[x] = bank[x] );
    }

    for (var i = 0; i < total; i++) {
      lorem.push(ipsum);
    }

    return lorem;
  }

}


/**
 * Color utility class
 */
export class Color {

  // Return context name from Hex value
  //
  static Name( value ) {
    const colors = Collection.Colors,
          obj = colors.find( x => x.color === value );

    if ( obj === undefined ) {
      return value;
    }
    return obj.name.toLowerCase();
  }


  // Return Hex value from context name
  //
  static Value( name ) {
    if ( ! name ) {
      return name;
    }

    if ( name.indexOf( '#' ) == 0 ) {
      return name;
    }

    const colors = Collection.Colors,
          obj = colors.find( x => x.name.toUpperCase() === name.toUpperCase() );

    if ( obj === undefined ) {
      return name;
    }
    return obj.color;
  }

}




/**
 * Icon
 */
export class Icons {

  static SVG( name ) {
    switch(name) {
      case 'section-header':
        return <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M3 18h12v-2H3v2zM3 6v2h18V6H3zm0 7h18v-2H3v2z"/><path fill="none" d="M0 0h24v24H0V0z"/></svg>;
        break;

      case 'hr':
        return <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M19 13H5v-2h14v2z"/><path d="M0 0h24v24H0z" fill="none"/></svg>;
        break;

    }
  }

}




/**
 * Simple object check.
 * @param item
 * @returns {boolean}
 */
function isObject(item) {
  return (item && typeof item === 'object');
}

/**
 * Deep merge two objects.
 * @param target
 * @param ...sources
 */
function mergeDeep(target, ...sources) {
  if (!sources.length) return target;
  const source = sources.shift();

  if (isObject(target) && isObject(source)) {
    for (const key in source) {
      if (isObject(source[key])) {
        if (!target[key]) Object.assign(target, { [key]: {} });
        mergeDeep(target[key], source[key]);
      } else {
        Object.assign(target, { [key]: source[key] });
      }
    }
  }

  return mergeDeep(target, ...sources);
}
