
export default class TheToolbar extends React.Component {

  constructor(props) {
    super(props);

    this.X = this.X.bind( this );
    this.set = this.set.bind( this );
  }


  X() {
    let { attributes, name, index, parent } = this.props;

    if ( name === undefined ) {
      return attributes;
    }

    if ( index !== undefined && parent !== undefined ) {
      attributes = Object.assign( {}, JSON.parse( JSON.stringify( attributes[ parent ][ index ] ) ) );
    }


    if ( name.indexOf( '.' ) === -1 ) {
      return attributes[ name ];
    }

    let keys = name.split( '.' );
    switch( keys.length ) {
      case 2:
        return attributes[ keys[0] ][ keys[1] ];
        break;

      case 3:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ];
        break;

      case 4:
        return attributes[ keys[0] ][ keys[1] ][ keys[2] ][ keys[3] ];
        break;
    }

    return attributes;
  }


  set( key, value ) {
    this.props.saveAttribute( key, value );
  }


  setOld( key, value ) {
    let { attributes, setAttributes, name, index } = this.props;

    if ( this.props.dataPrefix !== undefined ) {
      name = this.props.dataPrefix;
    }

    if ( index === undefined ) {
      let next = Object.assign( {}, JSON.parse( JSON.stringify( attributes[ name ] ) ) );
      next[ key ] = value;
      setAttributes( { [ name ]: next } );
    }
    else {
      let nextItems = attributes[ name +'_items' ].slice(0),
          nextItem  = {};

      Object.assign( nextItem, nextItems[ index ] );
      nextItem[ key ] = value;
      nextItems[ index ] = nextItem;
      setAttributes( { [ name +'_items' ]: nextItems } );
    }
  }

}
