
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, Dropdown, Button, IconButton, TextControl, ToggleControl, SelectControl, RangeControl, ColorPalette } = wp.components;
const { URLInput, MediaUpload } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar, TheToolbarContainer } from 'Components';
import { PickerIcon, Popup } from 'Toolbar';
import * as Util from 'Utils';



export default class ToolbarCard extends TheToolbar {

  render() {

    if ( ! this.props.isSelected ) {
      return null;
    }

    const X = this.X();


    return (
      <TheToolbarContainer {...this.props}>

        <Toolbar>


          {/* Link */}
          <Popup label={ __( 'Link' ) } icon="admin-links">
            <form
              className="block-library-button__inline-link the-url-input-form"
              onSubmit={ ( event ) => event.preventDefault() }>
              <URLInput
                value={ X.url }
                onChange={ (v) => this.set('url', v) }
              />
              <IconButton
                className={ 'external-'+ X.targetBlank }
                label={ __( 'Open in new tab' ) }
                icon="external"
                onClick={ () => this.set('targetBlank', ! X.targetBlank) }
              />
            </form>
          </Popup>


          {/* Background */}
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Background' ) }
                icon="admin-customizer"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <SelectControl
                  label={ __( 'Background type' ) }
                  value={ X.background.type }
                  onChange={ (v) => { this.set( 'background.type', v ) } }
                  options={ [
                    { value: 'color', label: 'Color' },
                    { value: 'image', label: 'Image' },
                  ] }
                />

                <hr />

                <ColorPalette
                  className="the-popover-color-palette1"
                  colors={ Util.Collection.Colors }
                  value={ X.background.color }
                  onChange={ (v) => this.set( 'background.color', v ) }
                />

                {/*
                <hr />

                <MediaUpload
                  onSelect={ (v) => this.set( 'background.image', v.url ) }
                  type="image"
                  value={ X.background.image }
                  render={ ( { open } ) => (
                    <div>
                      <div className="the-media-upload" onClick={ open }>
                        { ! X.background.image &&
                          <span>{ __( 'Choose an image' ) }</span>
                        }
                        { !! X.background.image &&
                          <img src={ X.background.image } />
                        }
                      </div>
                      { !! X.background.image &&
                        <Button onClick={ () => this.set( 'background.image', '' ) } isLink isDestructive>{ __( 'Remove image' ) }</Button>
                      }
                    </div>
                  ) }
                />
                */}

              </div>
            ) }
          />


          <MediaUpload
            onSelect={ (v) => this.set( 'background.image', v.url ) }
            allowedTypes="image"
            value={ X.background.image }
            render={ ( { open } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Edit image' ) }
                icon="format-image"
                onClick={ open }
              />
            ) }
          />


          {/* Overlay */}
          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'Overlay' ) }
                icon="welcome-add-page"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <RangeControl
                  label={ __( 'Overlay opacity' ) }
                  value={ X.overlay.opacity }
                  onChange={ (v) => this.set( 'overlay.opacity', v ) }
                  min={ 0 }
                  max={ 100 }
                  step="5"
                />

                <hr />

                <ColorPalette
                  className="the-popover-color-palette1"
                  colors={ Util.Collection.Colors }
                  value={ X.overlay.color }
                  onChange={ (v) => this.set( 'overlay.color', v ) }
                />

              </div>
            ) }
          />


          <Dropdown
            className="my-container-class-name"
            contentClassName="my-popover-content-classname"
            position="top center"
            renderToggle={ ( { isOpen, onToggle } ) => (
              <IconButton
                className="components-toolbar__control"
                label={ __( 'More options' ) }
                icon="admin-generic"
                onClick={ onToggle }
                aria-expanded={ isOpen }
              />
            ) }
            renderContent={ () => (
              <div className="the-popover-more-options">

                <ToggleControl
                  label={ __( 'Light text color' ) }
                  checked={ X.textWhite }
                  onChange={ (v) => this.set( 'textWhite', v ) }
                />

                <hr />

                <TextControl
                  label={ __( 'Extra class' ) }
                  value={ X.class }
                  onChange={ (v) => this.set( 'class', v ) }
                />

              </div>
            ) }
          />
        </Toolbar>

      </TheToolbarContainer>
    );
  }


}
