/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { Toolbar, ToolbarButton } = wp.components;
const { BlockControls } = wp.editor;

/**
 * Internal dependencies
 */
import { TheToolbar } from 'Components';
import * as Util from 'Utils';



export default class ToolbarSection extends TheToolbar {

  render() {
    const X = this.X();

    if ( X.isHeaderTag ) {
      return null;
    }

    return (
      <BlockControls>
        <Toolbar>

          <ToolbarButton
            label={ __( 'Hide section header' ) }
            icon={ Util.Icons.SVG('section-header') }
            isActive={ X.header.visible }
            onClick={ () => this.set( 'header.visible', ! X.header.visible ) }
          />

          { X.switchable &&
            <ToolbarButton
              label={ __( 'Switch sides' ) }
              icon="leftright"
              isActive={ X.switchSides }
              onClick={ () => this.set( 'switchSides', ! X.switchSides ) }
            />
          }

        </Toolbar>
      </BlockControls>
    );
  }

}
