/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image, Button } from 'Components';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    return (
      <Section {...props} name="section">
        <div class="row align-items-center h-100">

          <div class="col-md-8 mx-auto text-center">
            <RichText
              tagName="h1"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              className="lead mt-5"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
            <hr class="w-10 my-7"/>
            <Button {...props} name="btnPrimary" />
          </div>

          <div class="w-100"></div>

          <div class="col-auto align-self-end mx-auto mt-8 mt-lg-0">
            <div class="media align-items-center">
              <div class="mr-4">
                <Button {...props} name="btnPlay" />
              </div>
              <RichText
                tagName="div"
                className="media-body opacity-60"
                inlineToolbar
                onChange={ (v) => setAttributes({ txtLength: v }) }
                value={ attributes.txtLength }
              />
            </div>
          </div>

        </div>
      </Section>
    );
  }

}
