/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { Fragment } = wp.element;

/**
 * Internal dependencies
 */
import { TheComponent } from 'Components';
import * as Util from 'Utils';
import * as config from './config';
import { default as Toolbar } from './toolbar';



export default class Rating extends TheComponent {

  render() {
    this.props.saveAttribute = this.set;
    const X = this.X();


    let classes = classnames({
      'rating': true,
    }, X.class);

    let data = {
      'data-show-toolbar': this.props.name,
    };

    let stars = [];
    for (var i = 0; i < 5; i++) {
      var icon = 'fa-star-o';
      if (X.value >= i+1) {
        icon = 'fa-star';
      }
      else if (X.value === i+0.5) {
        icon = 'fa-star-half-o';
      }

      stars[i] = icon;
    }

    return (
      <Fragment>
        <Toolbar {...this.props} />

        <div class={ classes } { ...data }>
          {stars.map(x => <label className={"fa "+x} />)}
        </div>
      </Fragment>
    );
  }


  static get attributes() {
    return config.default;
  }

}
