/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
const { __ } = wp.i18n;
const { RichText } = wp.editor;
const { Component } = wp.element;

/**
 * Internal dependencies
 */
import { Section, Image } from 'Components';
import { TheBgImageUploader } from 'Editor';
import * as Util from 'Utils';



export default class Edit extends Component {

  render() {
    const { attributes, setAttributes, className, isSelected } = this.props;
    const props = { attributes, setAttributes, className, isSelected };

    const style = {
      backgroundImage: "url("+ attributes.imgBgFounder +")",
      minHeight: "200px",
      position: "relative",
    }

    return (
      <Section {...props} name="section">
        <div class="row no-gutters">

          <div class="col-md-4 bg-img" style={ style }>
            <TheBgImageUploader name="imgBgFounder" value={ attributes.imgBgFounder } setAttributes={ setAttributes } />
          </div>

          <div class="col-md-8 p-6 p-md-8">
            <RichText
              tagName="h4"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtTitle: v }) }
              value={ attributes.txtTitle }
            />
            <RichText
              tagName="p"
              className="lead"
              inlineToolbar
              onChange={ (v) => setAttributes({ txtLead: v }) }
              value={ attributes.txtLead }
            />
            <RichText
              tagName="p"
              inlineToolbar
              multiline
              onChange={ (v) => setAttributes({ txtText: v }) }
              value={ attributes.txtText }
            />
          </div>

        </div>
      </Section>
    );
  }

}
